import Vue from 'vue'
import Router from 'vue-router'
import Index from './views/index.vue'

// 弹窗
import CaseList from './views/modal/case-list.vue';
import CaseDetail from './views/modal/case-detail.vue';
import MediatorList from './views/modal/mediator-list.vue';
import MediatorDetail from './views/modal/mediator-detail.vue';
import Paperpage from './views/modal/paper.vue';
import CaseListNum from './views/modal/case-listNum.vue';
import CaseDetailNum from './views/modal/case-detailNum.vue';

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: Index,
      children: [
        {
          path: 'caseList',
          name: 'caseList',
          component: CaseList
        },
        {
          path: 'caseDetail',
          name: 'caseDetail',
          component: CaseDetail
        },
        {
          path: 'mediatorList',
          name: 'mediatorList',
          component: MediatorList
        },
        {
          path: 'mediatorDetail',
          name: 'mediatorDetail',
          component: MediatorDetail
        },
        {
          path: 'paperpage',
          name: 'paperpage',
          component: Paperpage
        },
        {
          path: 'caseListNum',
          name: 'caseListNum',
          component: CaseListNum
        },
        {
          path: 'caseDetailNum',
          name: 'caseDetailNum',
          component: CaseDetailNum
        }
      ]
    }
  ]
})
