<template>
  <div class="modal-header-wrap">
    <div class="modal-title" :class="[titleName === '协议书预览'? 'paperTitle':'']">
      <div class="paper-Title-Mid" v-if="titleName === '协议书预览'">协议书预览</div>
      <div class="iconfont iconfanhui icon-back" v-if="modalHasBack" @click="backroute">返回</div>
      <div class="titlename-box" v-else>{{titleName}}</div>
      <div class="iconfont iconguanbi close-detail" @click="hideModal"></div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  components: {
  },
  props: ['titleName'],
  name: "modalHeader",
  data() {
    return {
    }
  },
  methods: {
    ...mapActions(['setModal', 'changeRouterLength', 'setIsIndex', 'setmodalHasBack', 'setMinTitle']),
    hideModal() {
      this.setModal(false);
      this.setIsIndex(true);
      this.setmodalHasBack(false);
      this.setMinTitle('');
      this.$router.push({ path: '/'});
    },
    backroute() {
      this.$router.back(-1);
      this.changeRouterLength();
    }
  },
  watch: {
  },
  computed: {
    ...mapGetters(['modalHasBack', 'modalMinTitle'])
  },
  created() {
  },
  mounted() {
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../assets/scss/common.scss";
.modal-header-wrap {
  width: 100%;
  .modal-title {
    position: relative;
    width: 100%;
    height: .64rem;
    line-height: .64rem;
    color: $white;
    font-size: .28rem;
    text-align: center;
    font-weight: bold;
    background-image: linear-gradient(to right, #073c65, transparent);
    &.paperTitle {
      height: .64rem;
      line-height: .64rem;
      .paper-Title-Mid {
        font-size: .26rem;
        line-height: .64rem;
        font-weight: lighter;
        color: #26edff;
        text-align: center;
      }
    }
    .close-detail {
      position: absolute;
      right: 0;
      top: 0;
      font-size: .22rem;
      padding-right: .32rem;
      padding-left: .32rem;
      font-weight: normal;
      color: #32b6d7;
      cursor: pointer;
      &:hover {
        color: $white;
      }
    }
    .icon-back {
      position: absolute;
      left: 0;
      top: 0;
      font-size: .22rem;
      padding-right: .32rem;
      padding-left: .3rem;
      font-weight: normal;
      color: #32b6d7;
      cursor: pointer;
      &:hover {
        color: $white;
      }
    }
    .titlename-box {
      position: absolute;
      left: 0;
      top: 0;
      padding-left: .5rem;
      font-size: .24rem;
      font-weight: normal;
      color: $white;
      &::before {
        position: absolute;
        left: .3rem;
        top: .2rem;
        width: .05rem;
        height: .24rem;
        background-color: $blue;
        content: '';
      }
    }
  }
  .min-title {
    padding-left: .1rem;
    font-size: .18rem;
  }
}
</style>