// import * as types from '../mutation-types'
import api from '@/api/index';
import { Stats, stat } from 'fs';
const state = {
  allNumber: 0,
  crfcode: '', // csrf token
  collection: '', // 公钥
  currentTypeId: '',
  currentId: '',
  analysisDateType: 'MONTH',
  analysisResultData: {},
  alarms: [],
  queryPageNum: 1,
  queryAlarmStartDate: '',
  queryAlarmEndDate: '',
  queryAlarmStatus: '',
  queryAlarmResult: '',
  queryAlarmKey: '',
  outerCurrentTypeId: '',
  outerCurrentId: '',
  queryAlarmType: '',
  queryUserId: '',
  showModal: false,
  modalHasBack: false,
  routerLength: 0,
  outerAlarmDetail: null,
  outerFeedbackInfo: null,
  outerDispatchRecords: null,
  outerAlarmStaff: null,
  outerCaseInfo: null,
  caseId: '',
  outerbureauData: [],
  outerofficeData: [],
  outerofficeAll: [],
  mediators: [],
  outerMediatorNum: 0,
  justiceBureauTypeId: "255afee102374b60b770de3b601f0f0b",
  justiceOfficeTypeId: "98e06bc12e814282bd705b850723c6ed",
  outerMediatorDetail: {},
  personType: 'dispatcher',
  dispatchers: [],
  outerDispatcherNum: 0,
  selfPersonType: '',
  outerDispactcherDetail: {},
  isIndex: true,
  overviewData: {},
  alarmsCount: 0,
  modalMinTitle: '',
  justiceBureaus: [],
  searchByOrgId: '',
  cause: [],
  causeType: '',
  showOuterLoadingPage: true,
  indexDataArr: 0,
  showSendAjax: false,
  timeAjax: true,
  indexLoadTime: false,
  tempNum: undefined
};

const getters = {
  allNumber: state => state.allNumber,
  crfcode: state => state.crfcode,
  collection: state => state.collection,
  currentTypeId: state => state.currentTypeId,
  currentId: state => state.currentId,
  analysisDateType: state => state.analysisDateType,
  analysisResultData: state => state.analysisResultData,
  alarms: state => state.alarms,
  queryPageNum: state => state.queryPageNum,
  queryAlarmStartDate: state => state.queryAlarmStartDate,
  queryAlarmEndDate: state => state.queryAlarmEndDate,
  queryAlarmStatus: state => state.queryAlarmStatus,
  queryAlarmResult: state => state.queryAlarmResult,
  queryAlarmKey: state => state.queryAlarmKey,
  outerCurrentTypeId: state => state.outerCurrentTypeId,
  outerCurrentId: state => state.outerCurrentId,
  queryAlarmType: state => state.queryAlarmType,
  queryUserId: state => state.queryUserId,
  showModal: state => state.showModal,
  modalHasBack: state => state.modalHasBack,
  routerLength: state => state.routerLength,
  outerAlarmDetail: state => state.outerAlarmDetail,
  outerFeedbackInfo: state => state.outerFeedbackInfo,
  outerDispatchRecords: state => state.outerDispatchRecords,
  outerAlarmStaff: state => state.outerAlarmStaff,
  outerCaseInfo: state => state.outerCaseInfo,
  caseId: state => state.caseId,
  outerbureauData: state => state.outerbureauData,
  outerofficeData: state => state.outerofficeData,
  mediators: state => state.mediators,
  outerMediatorNum: state => state.outerMediatorNum,
  justiceBureauTypeId: state => state.justiceBureauTypeId,
  justiceOfficeTypeId: state => state.justiceOfficeTypeId,
  outerofficeAll: state => state.outerofficeAll,
  outerMediatorDetail: state => state.outerMediatorDetail,
  personType: state => state.personType,
  dispatchers: state => state.dispatchers,
  outerDispatcherNum: state => state.outerDispatcherNum,
  selfPersonType: state => state.selfPersonType,
  outerDispactcherDetail: state => state.outerDispactcherDetail,
  isIndex: state => state.isIndex,
  overviewData: state => state.overviewData,
  alarmsCount: state => state.alarmsCount,
  modalMinTitle: state => state.modalMinTitle,
  justiceBureaus: state => state.justiceBureaus,
  searchByOrgId: state => state.searchByOrgId,
  cause: state => state.cause,
  causeType: state => state.causeType,
  showOuterLoadingPage: state => state.showOuterLoadingPage,
  indexDataArr: state => state.indexDataArr,
  showSendAjax: state => state.showSendAjax,
  timeAjax: state => state.timeAjax,
  indexLoadTime: state => state.indexLoadTime,
  tempNum: state => state.tempNum
};

const actions = {
  getAllNum: ({ commit }, data) => commit('getAllNum', data),
  getLogin({ commit }, data) {
    // 登录函数
    api.getDistrict(data).then(json => {
      // console.log(json);
    });
  },
  AgetCrf({ commit }, inner) {
    // 获取token
    api.getCsrf().then(json => {
      commit('getCrf', json.data.result);
    });
  },
  changeIndexDataArr({ commit }, inner) {
    commit('ChangeIndexDataArr', inner);
  },
  AgetCollection({ commit }, inner) {
    // 获取公钥
    api.getColle().then(json => {
      commit('getCollection', json.data.result);
    });
  },
  AgetRank({ commit }, inner) {
    api
      .resultRank({
        datetype: state.analysisDateType,
        typeId: state.currentTypeId,
        id: state.currentId
      })
      .then(json => {
        commit('getRank', json.data);
      });
  },
  AgetAlarms({ commit }, inner) {
    api
      .alarmList({
        pageNum: state.queryPageNum,
        pageSize: 10,
        startDate: state.queryAlarmStartDate,
        endDate: state.queryAlarmEndDate,
        status: state.queryAlarmStatus,
        result: state.queryAlarmResult,
        key: inner.key,
        typeId: state.outerCurrentTypeId,
        id: state.outerCurrentId,
        type: state.queryAlarmType,
        userId: state.queryUserId,
        orgId: inner.orgId,
        cause: state.cause,
        causeType : state.causeType,
        pdfId:inner.pdfId
        
      })
      .then(result => {
        commit('getAlarmList', result.data);
      });
  },
  setPageNum({ commit }, inner) {
    commit('changePageNum', inner);
  },
  setModal({ commit }, inner) {
    commit('changeModal', inner);
  },
  setmodalHasBack({ commit }, inner) {
    commit('changehasBack', inner);
  },
  setCaseId({ commit }, inner) {
    state.caseId = inner;
    commit('setId', inner);
  },
  setShowOuterLoadingPage({ commit }, inner) {
    commit('setShowLoading', inner);
  },
  getCaseDetail({ commit }, inner) {
    commit('clearInfoDetail', inner);
    api
      .alarmDetailInfo({
        id: state.caseId
      })
      .then(result => {
        commit('setAlarmInfoDetail', result.data);
      });
  },
  changeRouterLength({ commit }, inner) {
    commit('reduceRouterLength', inner);
  },
  AgetSelectData({ commit }, inner) {
    api.orgList().then(result => {
      commit('getSelectData', result.data);
    });
  },
  AgetMediators({ commit }, inner) {
    api
      .mediatorList({
        typeId: state.outerCurrentTypeId,
        id: state.outerCurrentId,
        pageSize: 10,
        pageNum: state.queryPageNum,
        name: inner.name,
        orgId: inner.orgId
      })
      .then(result => {
        commit('setMediators', result.data);
      });
  },
  clearMediatorsList({ commit }, inner) {
    commit('clearList', inner);
  },
  ASetSelectType({ commit }, inner) {
    commit('setSelectType', inner);
  },
  changeOfficeData({ commit }, inner) {
    if (inner.type === '') {
      state.outerofficeData = state.outerofficeAll;
      return;
    }
    let arrData = [];
    state.outerofficeAll.forEach(e => {
      if (e.justiceBureauId == inner.val) {
        arrData.push(e);
      }
    })
    arrData.unshift({
      name: "全部司法所",
      id: "-1"
    });
    state.outerofficeData = arrData;
  },
  AgetMediatorDetailInfo({ commit }, inner) {
    api
      .staffDetailInfo({
        id: inner
      })
      .then(result => {
        commit('getMediatorDetailInfo', result.data.data);
      });
  },
  setClearPerson({ commit }, inner) {
    commit('setClearPersonState', inner);
  },
  setQueryAlarmStartDate({ commit }, inner) {
    commit('setqueryAlarmStartDate', inner);
  },
  setCauseType({ commit }, inner) {
    commit('setCauseType', inner);
  },
  clearCauseType({ commit }, inner) {
    commit('clearCauseType', inner);
  },
  setCauseData({ commit }, inner) {
    commit('setCause', inner);
  },
  clearCauseData({ commit }, inner) {
    commit('clearCause', inner);
  },
  setQueryAlarmEndDate({ commit }, inner) {
    commit('setqueryAlarmEndDate', inner);
  },
  setQueryAlarmStatus({ commit }, inner) {
    commit('setqueryAlarmStatus', inner);
  },
  setQueryAlarmResult({ commit }, inner) {
    commit('setqueryAlarmResult', inner);
  },
  setQueryAlarmKey({ commit }, inner) {
    commit('setqueryAlarmKey', inner);
  },
  setQueryAlarmType({ commit }, inner) {
    commit('setqueryAlarmType', inner);
  },
  setQueryUserId({ commit }, inner) {
    commit('setqueryUserId', inner);
  },
  setPersonType({ commit }, inner) {
    commit('SetPersonType', inner);
  },
  setSelfPersonType({ commit }, inner) {
    commit('SetSelfPersonType', inner);
  },
  Agetdispatchers({ commit }, inner) {
    api
      .employeeList({
        typeId: state.outerCurrentTypeId,
        id: state.outerCurrentId,
        pageSize: 10,
        pageNum: state.queryPageNum,
        name: inner.name,
        orgId: inner.orgId
      })
      .then(result => {
        // console.log('Agetdispatchers:', result.data);
        commit('getdispatchers', result.data);
      });
  },
  AgetdispatcherDetailInfo({ commit }, inner) {
    api
      .staffDetailInfo({
        id: inner
      })
      .then(result => {
        commit('getdispatcherDetailInfo', result.data.data);
      });
  },
  clearAlarm({ commit }, inner) {
    state.alarms = [];
  },
  setIsIndex({ commit }, inner) {
    state.isIndex = inner;
  },
  setOverviewData({ commit }, inner) {
    commit('changeOverviewData', inner);
  },
  setMinTitle({ commit }, inner) {
    state.modalMinTitle = inner;
  },
  setJusticeBureaus({ commit }, inner) {
    api.listJusticeBureaus().then(result => {
      commit("changeJusticeBureaus", result.data);
    });
  },
  updateSearchByOrgId({ commit }, inner) {
    state.searchByOrgId = inner;
  },
  clearCaseList({ commit }, inner) {
    commit('clearCaseList', inner);
  },
  changeOuterCase({ commit }, inner) {
    commit('setOuterCaseInfo', inner)
  },
  setShowSendAjax({ commit }, inner) {
    commit('SetShowSengAjax', inner);
  },
  setIndexLoadTime({ commit }, inner) {
    commit('SetIndexLoadTime', inner);
  },
  setTempNum({ commit }, inner) {
    commit('SetTempNum', inner);
  }
};

const mutations = {
  setShowLoading(state, inner) {
    state.showOuterLoadingPage = inner;
  },
  getAllNum(state, inner) {
  },
  setClearPersonState(state, inner) {
    state.outerMediatorDetail = {};
    state.outerDispactcherDetail = {};
  },
  getCrf(state, inner) {
    state.crfcode = '';
    state.crfcode = inner;
  },
  getPasWord(state, inner) {
    return inner;
  },
  getCollection(state, inner) {
    state.collection = '';
    state.collection = inner;
  },
  getRank(state, inner) {
    state.analysisResultData = inner;
  },
  getAlarmList(state, inner) {
    state.alarms = state.alarms.concat(inner.data);
    state.alarmsCount = inner.total;
  },
  changePageNum(state, inner) {
    if (inner === 'add') {
      state.queryPageNum++;
    } else if (inner === 'clear') {
      state.queryPageNum = 1;
    }
  },
  changeModal(state, inner) {
    state.showModal = inner;
  },
  changehasBack(state, inner) {
    state.modalHasBack = inner;
    if (inner) {
      state.routerLength++;
    } else {
      state.routerLength = 0;
    }
  },
  reduceRouterLength(state, inner) {
    state.routerLength--;
    if (state.routerLength === 0) {
      state.modalHasBack = false;
    }
  },
  clearInfoDetail(state, inner) {
    if (inner === 'outerAlarmDetail') {
      state.outerAlarmDetail = null;
      state.outerFeedbackInfo = null;
      state.outerCaseInfo = null;
      state.outerAlarmStaff = null;
      state.outerDispatchRecords = [];
    }
  },
  setAlarmInfoDetail(state, inner) {
    if (inner.alarmInfo) {
      state.outerAlarmDetail = inner.alarmInfo;
    }
    if (inner.feedbackInfo) {
      state.outerFeedbackInfo = inner.feedbackInfo;
    }
    if (inner.dispatchRecords) {
      state.outerDispatchRecords = inner.dispatchRecords;
    }
    if (inner.staff) {
      state.outerAlarmStaff = inner.staff;
    }
    if (inner.case) {
      state.outerCaseInfo = inner.case;
    }
  },
  setOuterCaseInfo(state, inner) {
    state.outerCaseInfo = inner;
  },
  setId(state, inner) {
    state.caseId = inner;
  },
  getSelectData(state, inner) {
    state.outerbureauData = inner.justiceBureau;
    state.outerofficeData = inner.justiceOffice;
    state.outerbureauData.unshift({
      name: "全部",
      id: "-1"
    });
    state.outerofficeData.unshift({
      name: "全部司法所",
      id: "-1"
    });
    state.outerofficeAll = state.outerofficeData;
  },
  setMediators(state, inner) {
    state.mediators = state.mediators.concat(inner.data);
    state.outerMediatorNum = inner.total;
  },
  clearList(state, inner) {
    state.mediators = [];
    state.dispatchers = [];
  },
  setSelectType(state, inner) {
    if (inner.type === 'area') {
      state.outerCurrentTypeId = state.justiceBureauTypeId;
      state.outerCurrentId = inner.val;
    } else if (inner.type === '') {
      state.outerCurrentTypeId = '';
      state.outerCurrentId = '';
    } else if (inner.type === 'place') {
      state.outerCurrentTypeId = state.justiceOfficeTypeId;
      state.outerCurrentId = inner.val;
    }
  },
  getMediatorDetailInfo(state, inner) {
    state.outerMediatorDetail = inner;
  },
  setqueryAlarmStartDate(state, inner) {
    state.queryAlarmStartDate = inner
  },
  setCause(state, inner) {
    state.cause = inner
  },
  setCauseType(state, inner) {
    state.causeType = inner;
  },
  clearCause(state, inner) {
    state.cause = [];
  },
  clearCauseType(state, inner) {
    state.causeType = '';
  },
  setqueryAlarmEndDate(state, inner) {
    state.queryAlarmEndDate = inner
  },
  setqueryAlarmStatus(state, inner) {
    state.queryAlarmStatus = inner
  },
  setqueryAlarmResult(state, inner) {
    state.queryAlarmResult = inner
  },
  setqueryAlarmKey(state, inner) {
    state.queryAlarmKey = inner
  },
  setqueryAlarmType(state, inner) {
    state.queryAlarmType = inner
  },
  setqueryUserId(state, inner) {
    state.queryUserId = inner
  },
  SetPersonType(state, inner) {
    state.personType = inner;
  },
  SetSelfPersonType(state, inner) {
    state.selfPersonType = inner;
  },
  getdispatchers(state, inner) {
    state.dispatchers = state.dispatchers.concat(inner.data);
    state.outerDispatcherNum = inner.total;
  },
  getdispatcherDetailInfo(state, inner) {
    state.outerDispactcherDetail = inner;
  },
  changeOverviewData(state, inner) {
    state.overviewData = inner
  },
  changeJusticeBureaus(state, inner) {
    state.justiceBureaus = inner
  },
  clearCaseList(state, inner) {
    state.alarms = [];
  },
  ChangeIndexDataArr(state, inner) {
    if (inner === 'add') {
      state.indexDataArr++;
    }else {
      state.indexDataArr = 0;
    }
    if (state.indexDataArr >= 3 && state.timeAjax && state.indexLoadTime) {
      state.showSendAjax = false;
      state.showOuterLoadingPage = false;
    }
  },
  SetShowSengAjax(state, inner) {
    if (inner === 'index') {
      state.showSendAjax = true;
      state.indexDataArr = 0;
      state.timeAjax = false;
      setTimeout(() => {
        state.timeAjax = true;
        console.log('time结束');
        console.log(state.timeAjax);
        console.log(state.indexDataArr);
        if (state.indexDataArr >= 3) {
          state.showSendAjax = false;
          state.showOuterLoadingPage = false;
        }
      }, 1000);
    }
  },
  SetIndexLoadTime(state, inner) {
    state.indexLoadTime = true;
    if (state.indexDataArr >= 3) {
      state.showSendAjax = false;
      state.showOuterLoadingPage = false;
    }
  },
  SetTempNum(state, inner) {
    state.tempNum = inner;
  }
};

export default {
  state,
  getters,
  actions,
  mutations
};
