<template>
  <div class="alert-title">
    <part-title titleName="实时警情信息" :ifWidth="true">
      <div class="progress-line-box" v-if="infoArr.length > 0">
        <div class="progress-line progress-line-first">
          <div :style="{'width': infoArr[0].pre}" class="progress-current-width">
            <span class="personlighter iconfont iconman" :class="{'new': newB}"></span>
            <span class="addperson" :class="{'add': addB}">+1</span>
          </div>
        </div>
        <div class="progress-line progress-line-second">
          <div :style="{'width': infoArr[1].pre}" class="progress-current-width">
            <span class="personlighter iconfont iconman" :class="{'new': newG}"></span>
            <span class="addperson" :class="{'add': addG}">+1</span>
          </div>
        </div>
        <div class="mask-disc">
          <div>{{infoArr[0].name}}: {{infoArr[0].num}}</div>
          <div>{{infoArr[1].name}}: {{infoArr[1].num}}</div>
        </div>
      </div>
    </part-title>
    <slot></slot>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import partTitle from "@/components/part-title.vue";

export default {
  components: {
    partTitle
  },
  name: "flowChart",
  props: ["alarmDetail", "personData"],
  data() {
    return {
      infoArr: [
        { pre: "80%", name: "调度员处置", num: 123 },
        { pre: "20%", name: "调解员处置", num: 123 }
      ],
      newB: false,
      newG: false,
      newBTime: null,
      newGTime: null,
      addB: false,
      addG: false,
      addBTime: null,
      addGTime: null
    };
  },
  methods: {
    ...mapActions([]),
    setpersonBlue() {
      this.newB = true;
      clearTimeout(this.newBTime);
      this.newBTime = setTimeout(() => {
        this.newB = false;
      }, 3000);
    },
    setpersonGreen() {
      this.newG = true;
      clearTimeout(this.newGTime);
      this.newGTime = setTimeout(() => {
        this.newG = false;
      }, 3000);
    },
    addpersonBlue() {
      this.addB = true;
      clearTimeout(this.addBTime);
      this.addBTime = setTimeout(() => {
        this.addB = false;
      }, 3000);
    },
    addpersonGreen() {
      this.addG = true;
      clearTimeout(this.addGTime);
      this.addGTime = setTimeout(() => {
        this.addG = false;
      }, 3000);
    },
    setInfoArr() {
      this.infoArr = [];
      this.infoArr.push({
        pre:
          Math.ceil(
            this.alarmDetail.dispatcherCount /
              this.personData.totalDispatcher *
              100
          ) + "%",
        name: "调度员处置",
        num: this.alarmDetail.dispatcherCount
      });
      this.infoArr.push({
        pre:
          Math.ceil(
            this.alarmDetail.mediatorCount / this.personData.totalMediator * 100
          ) + "%",
        name: "调解员处置",
        num: this.alarmDetail.mediatorCount
      });
      this.infoArr = this.infoArr.concat([]);
    }
  },
  watch: {
    alarmDetail() {
      this.setInfoArr();
    }
  },
  computed: {
    ...mapGetters([])
  },
  created() {},
  mounted() {
    // 设置人像闪烁
    this.setpersonBlue();
    this.setpersonGreen();
    // 设置+1动画
    // this.addpersonBlue();
    // this.addpersonGreen();
    this.setInfoArr();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../assets/scss/common.scss";
.alert-title {
  width: 100%;
  height: 100%;
  .progress-line-box {
    position: absolute;
    width: 100%;
    height: 100%;
    padding-top: .15rem;
    padding-right: .15rem;
    padding-left: 1.6rem;
    cursor: pointer;
    &:hover {
      .mask-disc {
        display: block;
      }
    }
    .mask-disc {
      position: absolute;
      right:  .3rem;
      top: 0;
      display: none;
      z-index: 2;
      min-height: .4rem;
      padding: .1rem .15rem;
      color: #fff;
      font-size:  .12rem;
      font-weight: lighter;
      line-height: .2rem;
      border-radius: .04rem;
      background-color: rgba(0, 0, 0, 0.7);
    }
    .progress-line {
      position: relative;
      width: 100%;
      height: .06rem;
      margin-bottom:  .12rem;
      border-top-right-radius: .03rem;
      border-bottom-right-radius: .03rem;
      background-color: #2c495f;
      .progress-current-width {
        position: absolute;
        left: 0;
        top: 0;
        min-width: 2%;
        height: 100%;
        border-top-right-radius: .03rem;
        border-bottom-right-radius: .03rem;
        .personlighter {
          position: absolute;
          right: -.22rem;
          top: - .12rem;
          width:  .3rem;
          height:  .3rem;
          line-height:  .3rem;
          font-size:  .12rem;
          border-radius: 50%;
        }
        .addperson {
          position: absolute;
          right: -.52rem;
          top: - .12rem;
          height:  .3rem;
          line-height:  .3rem;
          font-size:  .14rem;
          opacity: 0;
          &.add {
            animation: moveAdd 3s linear;
          }
        }
      }
    }
    @keyframes moveAdd {
      0% {
        top: - .12rem;
        opacity: 0;
      }
      2% {
        top: - .12rem;
        opacity: 1;
      }
      100% {
        top: -.42rem;
        opacity: 0;
      }
    }
    .progress-line-first {
      width: calc(100% - .24rem);
      margin-left: .24rem;
      .progress-current-width {
        background-color: #118be0;
        .personlighter {
          color: #118be0;
          &.new {
            animation: lighterBlue 1s linear infinite;
          }
        }
        .addperson {
          color: #118be0;
        }
      }
    }
    @keyframes lighterBlue {
      0% {
        background-image: radial-gradient(
          rgba(17, 139, 224, 0) 0%,
          transparent 100%
        );
      }
      25% {
        background-image: radial-gradient(
          rgba(17, 139, 224, 0.4) 1%,
          transparent 99%
        );
      }
      50% {
        background-image: radial-gradient(
          rgba(17, 139, 224, 0.8) 1%,
          transparent 99%
        );
      }
      75% {
        background-image: radial-gradient(
          rgba(17, 139, 224, 0.4) 1%,
          transparent 99%
        );
      }
      100% {
        background-image: radial-gradient(
          rgba(17, 139, 224, 0) 0%,
          transparent 100%
        );
      }
    }
    @keyframes lighterGreen {
      0% {
        background-image: radial-gradient(
          rgba(25, 221, 123, 0) 0%,
          transparent 100%
        );
      }
      25% {
        background-image: radial-gradient(
          rgba(25, 221, 123, 0.4) 1%,
          transparent 99%
        );
      }
      50% {
        background-image: radial-gradient(
          rgba(25, 221, 123, 0.8) 1%,
          transparent 99%
        );
      }
      75% {
        background-image: radial-gradient(
          rgba(25, 221, 123, 0.4) 1%,
          transparent 99%
        );
      }
      100% {
        background-image: radial-gradient(
          rgba(25, 221, 123, 0) 0%,
          transparent 100%
        );
      }
    }
    .progress-line-second {
      width: calc(100% - .16rem);
      margin-left: .16rem;
      .progress-current-width {
        background-color: #19dd7b;
        .personlighter {
          color: #19dd7b;
          &.new {
            animation: lighterGreen 1s linear infinite;
          }
        }
        .addperson {
          color: #19dd7b;
        }
      }
    }
  }
}
</style>