<template>
  <div class="case-detail">
    <modal-header :titleName="titleName"></modal-header>
    <div class="case-detail-title clearfix">
      <div class="number-wrap pull-left">
        <i class="iconfont iconanli"></i>
        <span>案号：{{outerAlarmDetail.properties.eventNumber}}</span>
      </div>
      <div class="type-wrap pull-right clearfix">
        <span class="wait pull-left" v-if="outerAlarmDetail.properties.status==='ACCEPTED'">待分派</span>
        <span
          class="bureaus pull-left"
          v-if="outerAlarmDetail.properties.status==='JB_DISPATCHED'"
        >已派区局</span>
        <span
          class="office pull-left"
          v-if="outerAlarmDetail.properties.status==='JO_DISPATCHED'||outerAlarmDetail.properties.status==='HZ_MC_DISPATCHED'"
        >已派司法所/专调</span>
        <span
          class="committee pull-left"
          v-if="outerAlarmDetail.properties.status==='MC_DISPATCHED'"
        >已派调委会</span>
        <span class="end pull-left" v-if="outerAlarmDetail.properties.status==='FINISHED' && !showPrint(outerAlarmDetail)">已处置</span>
        <span
          class="receive pull-left"
          v-if="outerAlarmDetail.properties.status==='JO_ACCEPTED'"
        >司法所已接收</span>
        <span class="ready pull-left" v-if="showPrint(outerAlarmDetail)" @click="showPaper">
        <!-- <span class="ready pull-left" @click="showPaper"> -->
          达成协议
          <div class="tooltip-item pull-right">
            <i class="iconfont iconsuccess"></i>
            <div class="tool-tip">点击预览协议书</div>
          </div>
        </span>
      </div>
    </div>
    <div class="case-content" v-if="outerDispatchRecords">
      <el-scrollbar class="scroll-deatil full-content">
        <div class="message-wrap clearfix">
          <div class="essential-wrap pull-left">
            <!-- 基本信息 -->
            <div class="iner-content-box">
              <div class="title">
                <span
                  class="title-icon"
                  style="background-image: url(images/detail_title_icon.png)"
                ></span>
                <span class="title-name">基本信息</span>
              </div>
              <div class="iner-box">
                <table class="detail-table">
                  <tr>
                    <td>报警人</td>
                    <td>{{outerAlarmDetail.properties.callerName}}</td>
                  </tr>
                  <tr>
                    <td>案由</td>
                    <td>{{outerAlarmDetail.$displays.cause}}</td>
                  </tr>
                  <tr>
                    <td>首次报警时间</td>
                    <td>{{outerAlarmDetail.$displays.callTime}}</td>
                  </tr>
                  <tr>
                    <td>警情描述</td>
                    <td>{{outerAlarmDetail.properties.desc}}</td>
                  </tr>
                  <tr>
                    <td>事发地址</td>
                    <td>{{outerAlarmDetail.properties.eventAddress}}</td>
                  </tr>
                  <tr>
                    <td>联系电话</td>
                    <td>{{outerAlarmDetail.properties.callerPhone}}</td>
                  </tr>
                  <tr>
                    <td>联系地址</td>
                    <td>{{outerAlarmDetail.properties.callerAddress}}</td>
                  </tr>
                </table>
              </div>
            </div>
            <!-- 处置信息 -->
            <div class="iner-content-box" v-if="outerFeedbackInfo">
              <div class="title">
                <span
                  class="title-icon"
                  style="background-image: url(images/detail_title_icon.png)"
                ></span>
                <span class="title-name">处置信息</span>
              </div>
              <div class="iner-box">
                <table class="detail-table">
                  <tr>
                    <td>处置情况</td>
                    <td>{{outerFeedbackInfo.$displays.newResult}}</td>
                  </tr>
                  <tr>
                    <td>情况描述</td>
                    <td>{{outerFeedbackInfo.$displays.desc}}</td>
                  </tr>
                  <tr>
                    <td>处置人员</td>
                    <td
                      :class="{'clicktrue': outerFeedbackInfo.$displays.handler }"
                      @click="callerDetail(outerAlarmStaff)"
                    >{{outerFeedbackInfo.$displays.handler}}</td>
                  </tr>
                  <tr>
                    <td>所在人群</td>
                    <td>{{outerFeedbackInfo.$displays.personType}}</td>
                  </tr>
                  <tr>
                    <td>处置日期</td>
                    <td>{{outerFeedbackInfo.$displays.handlerDate}}</td>
                  </tr>
                  <tr>
                    <td>是否现场处置</td>
                    <td>{{outerFeedbackInfo.$displays.onsite}}</td>
                  </tr>
                  <tr>
                    <td>当事人是否接受了告知</td>
                    <td>{{outerFeedbackInfo.$displays.isReceive}}</td>
                  </tr>
                  <tr>
                    <td>拒绝方式</td>
                    <td>{{outerFeedbackInfo.$displays.refusalMode}}</td>
                  </tr>
                  <tr>
                    <td>是否告知诉求途径</td>
                    <td>{{outerFeedbackInfo.$displays.isChannel}}</td>
                  </tr>
                </table>
              </div>
            </div>
            <div class="iner-content-box" v-if="outerCaseInfo">
              <div class="title">
                <span
                  class="title-icon"
                  style="background-image: url(images/detail_title_icon.png)"
                ></span>
                <span class="title-name">调解案件信息</span>
                <div class="pull-right" v-if="showPrint(outerCaseInfo)">
                  <!-- <a target="_blank" class="line-more" :href="getPrintUrl(outerCaseInfo)">打印材料</a> -->
                </div>
              </div>
              <div class="iner-box">
                <table class="detail-table">
                  <tr>
                    <td>名称：</td>
                    <td>{{outerCaseInfo.$displays.name}}</td>
                  </tr>
                  <tr>
                    <td>状态：</td>
                    <td>{{outerCaseInfo.$displays.status}}</td>
                  </tr>
                  <tr>
                    <td>纠纷类型</td>
                    <td>{{outerCaseInfo.$displays.businessType}}</td>
                  </tr>
                  <tr>
                    <td>案号</td>
                    <td>{{outerCaseInfo.$displays.num}}</td>
                  </tr>
                  <tr>
                    <td>协议日期</td>
                    <td>{{outerCaseInfo.$displays.dateFinished}}</td>
                  </tr>
                  <tr>
                    <td>纠纷事由</td>
                    <td>{{outerCaseInfo.$displays.content}}</td>
                  </tr>
                  <tr>
                    <td>调委会</td>
                    <td>{{outerCaseInfo.$displays.mediationCommittee}}</td>
                  </tr>
                  <tr>
                    <td>人民调解员</td>
                    <td>{{outerCaseInfo.$displays.mediator}}</td>
                  </tr>
                </table>
              </div>
            </div>
          </div>
          <!-- 右侧 视频连线和处置流程 -->
          <div class="process-wrap pull-right">
            <!-- 视频连线 -->
            <div class="iner-content-box">
              <div class="title">
                <span
                  class="title-icon"
                  style="background-image: url(images/detail_title_icon.png)"
                ></span>
                <span class="title-name">视频连线</span>
              </div>
              <div class="iner-box">
                <div class="video-online-wrap clearfix">
                  <div class="message-pic-wrap pull-left clearfix">
                    <div
                      class="pic-box pull-left"
                      :style="{'background-image': 'url(' + fileUrl(outerAlarmStaff.properties.portrait) + ')'}"
                    ></div>
                    <div class="message-iner-box pull-right">
                      <span class="name">{{outerAlarmStaff.properties.name}}</span>
                      <p>{{outerAlarmStaff.$displays.practiceOrg}}</p>
                    </div>
                  </div>
                  <div class="online-btn-box pull-right" @click="contact">
                    <i class="iconfont iconshipinlianxian"></i>
                    <br>
                    <span>视频连线</span>
                  </div>
                </div>
              </div>
            </div>
            <!-- 处置流程 -->
            <div class="iner-content-box">
              <div class="title">
                <span
                  class="title-icon"
                  style="background-image: url(images/detail_title_icon.png)"
                ></span>
                <span class="title-name">处置流程</span>
              </div>
              <div class="iner-box">
                <ul class="broadcast-detail-record" v-if="outerDispatchRecords.length > 0">
                  <li
                    class="broadcast-record-item"
                    v-for="(item, index) in outerDispatchRecords"
                    :key="index"
                  >
                    <div class="record-time">{{item.$displays.date}}</div>
                    <div class="record-content">{{item.properties.description}}</div>
                  </li>
                </ul>
                <div class="no-message" v-if="outerDispatchRecords.length === 0">
                  <i class="iconfont iconzanwuxinxi"></i>
                  <p>暂无信息</p>
                </div>
              </div>
            </div>
          </div>
        </div>
      </el-scrollbar>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import modalHeader from '@/components/modal-header.vue';
import api from '@/api/index.js';
export default {
  components: {
    modalHeader
  },
  name: "caseDetail",
  data() {
    return {
      titleName: '实时警情播报'
    }
  },
  methods: {
    ...mapActions(['getCaseDetail', 'setSelfPersonType', 'AgetMediatorDetailInfo', 'setmodalHasBack', 'setIsIndex']),
    contact() {
      api.contact();
    },
    fileUrl(id) {
      if (id) {
        return api.fileUrl + id + "/view?thumb=200x200";
      }
      return "./images/portrait.jpg";
    },
    getPrintUrl(caseinfo) {
      return api.baseUrl + '/mediation/mbmCase/index_import.jsp?caseId=' + caseinfo.properties.$id;
    },
    showPrint(alarmInfo) {
      if(!alarmInfo){
        return false;
      }
      if (alarmInfo.properties.pdfId) {
          return true;
      } else {
        return false;
      }
    },
    callerDetail(item) {
      if (item.properties.$typeId == "8f3cd2165cb540b189ce86c143fa3afd") {
        this.setSelfPersonType('dispatcher');
      } else {
        this.setSelfPersonType('mediator');
        this.AgetMediatorDetailInfo(item.properties.$id);
      }
      this.setmodalHasBack(true);
      this.$router.push({ path: '/mediatorDetail' });
    },
    showPaper() {
      this.setmodalHasBack(true);
      this.$router.push({ path: '/paperpage'});
    }
    
  },
  watch: {},
  computed: {
    ...mapGetters(['outerAlarmDetail', 'outerFeedbackInfo', 'outerDispatchRecords', 'outerAlarmStaff', 'outerCaseInfo'])
  },
  created() {
    let that = this;
    setTimeout(() => {
      this.setIsIndex(false);
    }, 500);
    this.getCaseDetail();
  },
  mounted() { }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../../assets/scss/common.scss";

.case-detail {
  position: absolute;
  top: 1.26rem;
  left: 0;
  right: 0;
  margin: auto;
  z-index: 3;
  width: 80%;
  font-size: .16rem;
  height: calc(100% - 2.52rem);
  background-color: #002543;
  .case-content {
    width: 100%;
    height: calc(100% - 1.34rem);
    padding: .3rem .12rem .3rem .3rem;
  }
  .scroll-deatil {
    width: 100%;
    height: 100%;
  }
  .case-detail-title {
    width: 100%;
    height: auto;
    padding-left: .3rem;
    padding-right: .3rem;
    color: $white;
    background: #1c6c9b;
    .number-wrap {
      height: .7rem;
      line-height: .7rem;
      font-size: .28rem;
      .iconfont {
        margin-right: .15rem;
        font-size: .28rem;
      }
    }
    .type-wrap {
      height: .7rem;
      line-height: .7rem;
      font-size: .2rem;
      font-weight: inherit;
      span {
        position: relative;
        display: block;
        &::after {
          position: absolute;
          top: 0;
          bottom: 0;
          left: -.15rem;
          width: .08rem;
          height: .08rem;
          margin: auto;
          border-radius: 50%;
          content: "";
          background: #18dd7b;
        }
        &.bureaus::after {
          background-color: #e83d12;
        }
        &.office::after {
          background-color: #ede73b;
        }
        &.committee::after {
          background-color: $blue;
        }
        &.end::after {
          background-color: $green;
        }
        &.receive::after {
          background-color: #743eff;
        }
        &.ready::after {
          background-color: #18dd7b;
        }
      }
      .ready {
        cursor: pointer;
        animation: mymove 1s 3;
        -webkit-animation: mymove 1s 3; /*Safari and Chrome*/
      }
    }
  }
  .message-wrap {
    width: calc(100% - .18rem);
    margin-right: .18rem;
    height: auto;
    .essential-wrap {
      width: calc(60% - .08rem);
      height: auto;
    }
    .process-wrap {
      width: calc(40% - .08rem);
      height: auto;
    }
    .iner-content-box {
      width: 100%;
      height: auto;
      margin-bottom: .16rem;
      background: #093c63;
      .title {
        width: 100%;
        height: .7rem;
        padding-left: .2rem;
        padding-right: .2rem;
        line-height: .7rem;
        text-align: left;
        color: $white;
        font-size: .24rem;
      }
      .title-icon {
        width: .24rem;
        height: .18rem;
        margin-right: .12rem;
        background-size: 100% 100%;
        display: inline-block;
      }
      .iner-box {
        width: 100%;
        height: auto;
        color: $white;
        padding-left: .24rem;
        padding-right: .24rem;
        padding-bottom: .24rem;
      }
    }
  }
  .video-online-wrap {
    width: 100%;
    height: auto;
    .message-pic-wrap {
      width: calc(100% - .96rem);
      height: auto;
      .pic-box {
        width: .8rem;
        height: .96rem;
        background-size: cover;
        background-repeat: no-repeat;
      }
      .message-iner-box {
        width: calc(100% - .8rem);
        height: auto;
        padding-left: .15rem;
        padding-right: .15rem;
        text-align: left;
        p {
          margin-bottom: 0;
          margin-top: .2rem;
        }
      }
    }
    .online-btn-box {
      width: .96rem;
      height: .96rem;
      text-align: center;
      font-size: .18rem;
      background: #0a92d9;
      border-radius: .04rem;
      cursor: pointer;
      .iconfont {
        display: inline-block;
        padding-top: .14rem;
        font-size: .4rem;
      }
    }
    .online-btn-box:hover {
      background: lighten(#0a92d9, 10%);
    }
  }
  .broadcast-detail-record {
    list-style: none;
    padding: 0;
    margin: 0;
    width: 100%;
  }
  .broadcast-record-item {
    position: relative;
    padding: .16rem 0 .14rem .25rem;
    margin: 0;
    list-style: none;
    width: 100%;
    line-height: .3rem;
    font-size: .16rem;
    color: $white;
    text-align: left;
    font-weight: lighter;
    .record-time {
      color: #a5afbb;
      font-weight: lighter;
    }
    &::before {
      position: absolute;
      left: 0;
      top: .29rem;
      width: .02rem;
      height: calc(100% - .18rem);
      background-color: #40dbf8;
      content: "";
    }
    &::after {
      position: absolute;
      left: -.04rem;
      top: .15rem;
      width: .1rem;
      height: .1rem;
      border-radius: 50%;
      background-color: #40dbf8;
      content: "";
    }
  }
  .no-message {
    width: 100%;
    height: auto;
    padding-top: .6rem;
    padding-bottom: .6rem;
    color: #51758f;
    font-size: .18rem;
    .iconfont {
      font-size: .6rem;
    }
  }
  .clicktrue {
    cursor: pointer;
    &:hover {
      color: $white;
    }
  }
  .name {
    font-size: .22rem;
    color: $white;
  }
  .line-more {
    font-size: .18rem;
    color: #d7eaf2;
  }
  // 达成预览效果小图标
  .tooltip-item {
    position: relative;
    height: .7rem;
    padding-left: .1rem;
    cursor: pointer;

    .iconsuccess {
      font-size: .24rem;
    }
    .tool-tip {
      display: none;
      position: absolute;
      top: .65rem;
      right: -.3rem;
      width: 1.5rem;
      font-size: .16rem;
      line-height: .4rem;
      text-align: center;
      border-radius: .04rem;
      background-color: $black;
      color: $white;
      z-index: 1;
      &::after {
        position: absolute;
        top: -.16rem;
        right: .32rem;
        width: 0;
        height: 0;
        content: "";
        border-width: .08rem .08rem .08rem .08rem;
        border-style: solid;
        border-color: transparent transparent $black transparent;
      }
    }
    &:hover {
      .tool-tip {
        display: block;
      }
    }
  }
}
@keyframes mymove {
  from {
    color: #26edff;
  }
  to {
    color: $white;
  }
}
@-webkit-keyframes mymove {
  from {
    color: #26edff;
  }
  to {
    color: $white;
  }
}
</style>