//日期格式化
const format = function (date, fmt) { //author: meizz 
    var o = {
        "M+": date.getMonth() + 1, //月份 
        "d+": date.getDate(), //日 
        "H+": date.getHours(), //小时 
        "m+": date.getMinutes(), //分 
        "s+": date.getSeconds(), //秒 
        "q+": Math.floor((date.getMonth() + 3) / 3), //季度 
        "S": date.getMilliseconds() //毫秒 
    };
    if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (date.getFullYear() + "").substr(4 - RegExp.$1.length));
    for (var k in o)
        if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
    return fmt;
}
/**
* 获取指定日期的周的第一天、月的第一天、季的第一天、年的第一天
* @param date new Date()形式，或是自定义参数的new Date()
* @returns 返回值为格式化的日期，yy-mm-dd
*/
//日期格式化，返回值形式为yy-mm-dd
function timeFormat(date) {
    if (!date || typeof (date) === "string") {
        this.error("参数异常，请检查...");
    }
    var y = date.getFullYear(); //年
    var m = date.getMonth() + 1; //月
    var d = date.getDate(); //日

    return y + "-" + m + "-" + d;
}

//获取这周的周一
function getFirstDayOfWeek(date,fmt) {

    var weekday = date.getDay() || 7; //获取星期几,getDay()返回值是 0（周日） 到 6（周六） 之间的一个整数。0||7为7，即weekday的值为1-7

    date.setDate(date.getDate() - weekday + 1);//往前算（weekday-1）天，年份、月份会自动变化
    return format(date,fmt);
}

//获取当月第一天
function getFirstDayOfMonth(date,fmt) {
    date.setDate(1);
    return format(date,fmt);
}

//获取当季第一天
function getFirstDayOfSeason(date,fmt) {
    var month = date.getMonth();
    if (month < 3) {
        date.setMonth(0);
    } else if (2 < month && month < 6) {
        date.setMonth(3);
    } else if (5 < month && month < 9) {
        date.setMonth(6);
    } else if (8 < month && month < 11) {
        date.setMonth(9);
    }
    date.setDate(1);
    return format(date,fmt);
}

//获取当年第一天
function getFirstDayOfYear(date,fmt) {
    date.setDate(1);
    date.setMonth(0);
    return format(date,fmt);
}

export default {
    format: format,
    getFirstDayOfYear: getFirstDayOfYear,
    getFirstDayOfSeason: getFirstDayOfSeason,
    getFirstDayOfMonth: getFirstDayOfMonth,
    getFirstDayOfWeek: getFirstDayOfWeek
}