<template>
  <div class="pie-wrap">
    <div :id="chartId" style="width: 100%;height: 100%;"></div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import pie from "echarts";

export default {
  components: {},
  props: ['chartId', 'dataList', 'pieType'],
  name: "pie-echart",
  data() {
    return {
      data: []
    };
  },
  methods: {
    ...mapActions([]),
    setChart() {
      var totalCount = this.dataList.totalCount;
      this.data = this.dataList.list;
      // let this_ = this;
      let myChart = pie.init(document.getElementById(this.chartId));
      let nameArr = [];
      if (this.data) {
        this.data.forEach(e => {
          nameArr.push(e.name);
        })
      }
      let option = {
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b}: {c} ({d}%)',
          position: 'right'
        },
        series: [
          {
            name: '访问来源',
            type: 'pie',
            radius: ['40%', '60%'],
            avoidLabelOverlap: false,
            hoverOffset: 5,
            label: {
              normal: {
                show: true,
                color: '#fff',
                formatter: data => {
                  let precent = (data.value / totalCount) * 100;
                  precent = precent.toFixed(2);
                  if (totalCount === 0) {
                    precent = 0;
                  }
                  return precent + '%';
                }
              }
            },
            labelLine: {
              normal: {
                show: true
              }
            },
            data: this.data
          }
        ],
        color: ['#18dd7b', '#f43aed', '#3495e7', '#f9b656']
      };
      if(this.pieType) {
        option.series[0].label.normal.show = false;
        option.series[0].labelLine.normal.show = false;
        option.series[0].center = ['30%', '50%'];
        option.series[0].name = '能力评析';
        option.legend = {
          orient: 'vertical',
          y: 'center',
          right: '10%',
          align: 'left',
          data: nameArr,
          textStyle: {
            color: '#fff',
            fontSize: '16'
          },
          itemWidth: 8,
          itemHeight: 8,
          itemGap: 15
        };
        option.color = ['#0ace9e', '#11cfe0', '#118be0', '#ede73b', '#4068e7'];
      }
      myChart.setOption(option);

      //建议加上以下这一行代码，不加的效果图如下（当浏览器窗口缩小的时候）。超过了div的界限（红色边框）
      window.addEventListener('resize', function () {
        myChart.resize();
      });
    }
  },
  watch: {
    // chartData(newVal) {}
    dataList(newVal) {
      this.setChart();
    }
  },
  computed: {
    // ...mapGetters(['showOuterLoadingPage'])
  },
  created() { },
  mounted() {
    this.setChart();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../assets/scss/common.scss";
.pie-wrap {
  width: 100%;
  height: 100%;
  #type-chart {
    width: 100%;
    height: 100%;
  }

  #pie-one {
    width: 100%;
    height: 100%;
  }
  #pie-two {
    width: 100%;
    height: 100%;
  }
  #pie-three {
    width: 100%;
    height: 100%;
  }
  #radar-one {
    width: 100%;
    height: 2.4rem;
  }
}
</style>