<template>
  <div class="index-wrapper">
    <header-vue></header-vue>
    <div class="index-content clearfix">
      <div class="pull-left index-charts">
        <left-part></left-part>
      </div>
      <div class="pull-left index-flowchart">
        <flow-chart :alarmDetail="detailData"></flow-chart>
      </div>
      <div class="pull-left index-alarminfo">
        <div class="right-sider">
          <div class="sider-up">
            <div class="up-row clearfix dispose">
              <div class="pull-left label-wrap">
                <div class="outer">实有人员力量</div>
                <div class="inner"></div>
              </div>
              <div class="up-content pull-left">
                <div class="pull-left up-item clearfix" @click="showtotalDispatcher">
                  <div class="pull-left type">调度员总计：</div>
                  <div class="pull-left num">{{personData.totalDispatcher}}</div>
                  <div class="pull-left unit">人</div>
                </div>
                <div class="pull-right up-item clearfix" @click="showtotalMediator">
                  <div class="pull-left type">调解员总计：</div>
                  <div class="pull-left num">{{personData.totalMediator}}</div>
                  <div class="pull-left unit">人</div>
                </div>
              </div>
            </div>
            <div class="up-row clearfix ">
              <div class="pull-left label-wrap">
                <div class="outer">今日处置力量</div>
                <div class="inner"></div>
              </div>
              <div class="up-content pull-left clearfix">
                <div class="pull-left up-item">
                  <div class="pull-left type">调度员：</div>
                  <div class="pull-left num">{{detailData.dispatcherCount}}</div>
                  <div class="pull-left unit">人</div>
                </div>
                <div class="pull-right up-item  clearfix">
                  <div class="pull-left type">调解员：</div>
                  <div class="pull-left num">{{detailData.mediatorCount}}</div>
                  <div class="pull-left unit">人</div>
                </div>
              </div>
            </div>
          </div>
          <div class="sider-down">
            <alert-title :alarmDetail="detailData" :personData="personData">
              <div class="sider-down-inner">
                <el-scrollbar class="sider-scroll">
                  <div class="case-list-wrap">
                    <div v-for="(item,index) in earlyListData" :key="index" class="early-item">
                      <early-info :itemData="item"></early-info>
                    </div>
                  </div>
                </el-scrollbar>
              </div>
            </alert-title>
          </div>
        </div>
      </div>
    </div>
    <div class="modal-router" v-if="showModal">
      <div class="animate-box" :class="{'move-in': routerLength === 0 && isIndex}">
        <router-view></router-view>
      </div>
    </div>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
import headerVue from "@/components/header.vue";
import flowChart from "@/components/flowChart.vue";
import earlyInfo from "@/components/early-info";
import alertTitle from "@/components/alert-title";
import leftPart from "./leftPart.vue";
import apiService from "../api/index.js";

export default {
  components: {
    headerVue,
    flowChart,
    earlyInfo,
    alertTitle,
    leftPart
  },
  name: "index",
  data() {
    return {
      ws:null,
      personData: {},
      detailData: {},
      earlyListData: [
        {
          time: "2019-03-20 19:35:18",
          caseName: "家庭婚姻纠纷",
          party: "徐培明",
          address: "浦东新区起来大厦",
          office: "徐汇区司法局",
          officeImg: require("../../public/images/portrait.jpg"),
          caseNum: 156,
          mediator: "刘明（调解员）",
          mediatorhead: require("../../public/images/head.png"),
          mediatorNum: 156,
          step: 1
        },
        {
          time: "2019-03-20 19:35:18",
          caseName: "邻里纠纷",
          party: "徐培明",
          address: "浦东新区起来大厦",
          office: "徐汇区司法局",
          officeImg: require("../../public/images/portrait.jpg"),
          caseNum: 156,
          mediator: "刘明（调解员）",
          mediatorhead: require("../../public/images/head.png"),
          mediatorNum: 156,
          step: 2
        },
        {
          time: "2019-03-20 19:35:18",
          caseName: "邻里纠纷",
          party: "徐培明",
          address: "浦东新区起来大厦",
          office: "徐汇区司法局",
          officeImg: require("../../public/images/portrait.jpg"),
          caseNum: 156,
          mediator: "刘明（调解员）",
          mediatorhead: require("../../public/images/head.png"),
          mediatorNum: 156,
          step: 2
        }
      ],
      totalTemp: 0,
      bureauTotalCount: 0
    };
  },
  methods: {
    ...mapActions(['setModal', 'setmodalHasBack', 'setPersonType','AgetMediators', 'Agetdispatchers', 'changeIndexDataArr', 'setTempNum']),
    getPersonList() {
      let that = this;
      apiService.getLeftDate({orgId:this.searchByOrgId}).then(result => {
        let data = result.data;
        that.personData = data.personData;
        that.changeIndexDataArr('add');
      });
    },
    showtotalMediator() {
      this.setModal(true);
      this.setPersonType('mediator');
      this.$router.push({ path: '/mediatorList' });
    },
    showtotalDispatcher() {
      this.setModal(true);
      this.setPersonType('dispatcher');
      this.$router.push({ path: '/mediatorList' });
    },

    
    websocketData(that) {
      let _that = this;
      if ("WebSocket" in window) {
      // 打开一个 web socket
      if (this.ws) {
        this.ws.close();
      }
      this.ws = new WebSocket(
        "ws://" +
        apiService.baseUrl.replace("http://", "") +
        "/service/websocket/alarmRegulation?orgId=" + that.searchByOrgId
      );
      console.log(that.searchByOrgId)
      const ws = this.ws;
      ws.onopen = function (evt) {
        // Web Socket 已连接上，使用 send() 方法发送数据
        console.log("已链接");
      };

      ws.onerror = function(){
        console.log('websocket error');
      }

      ws.onmessage = function (evt) {
        var received_msg = evt.data;
        _that.changeIndexDataArr('add');
        let data = JSON.parse(received_msg);
        console.log('onmessage:', data);
        if((that.searchByOrgId === '' || that.searchByOrgId === '1919f623b5d140598b4eb80660715055') && data.detailData.totalCount > 0  && data.detailData.bureauTotalCount > 0) {
          let temp = data.detailData.totalCount - data.detailData.bureauTotalCount;
          _that.setTempNum(temp);
          // _that.totalTemp = data.detailData.totalCount; 
          // _that.bureauTotalCount = data.detailData.bureauTotalCount;
        }
        // else if(_that.tempNum >=0 && data.detailData.totalCount > 0 && data.detailData.bureauTotalCount > 0){
        //   if(data.detailData.totalCount !== _that.totalTemp) {
        //     let add = data.detailData.totalCount - _that.totalTemp;
        //     console.log('add:'+ add);
        //     _that.setTempNum(_that.totalTemp + add);
        //   }
        //   if(data.detailData.bureauTotalCount !== _that.bureauTotalCount && (that.searchByOrgId === '' || that.searchByOrgId === '1919f623b5d140598b4eb80660715055')) {
        //     let miu = data.detailData.totalCount - _that.bureauTotalCount;
        //     console.log('miu:'+ mius);
        //     _that.setTempNum(_that.totalTemp - miu);
        //   }
        // }
        if (data.list) {
          let dataArr = [];
          data.list.forEach(e => {
            let id = e.alarmInfo.$displays.$id;
            let time = 0;
            data.list.forEach(ele => {
              if(ele.alarmInfo.$displays.$id === id) {
                time++;
                if(time === 1) {
                  dataArr.push(ele);
                }
              }
            })
          })
          that.earlyListData = dataArr;
          that.detailData = data.detailData;
        } else {
          if(!data.alarmData){
            that.detailData = data.detailData
            return;
          }
          if (that.earlyListData.length == 20) {
            that.earlyListData.unshift(data.alarmData)
            let arr = that.earlyListData
            arr.pop()
            that.earlyListData = arr

          } else {
            that.earlyListData.unshift(data.alarmData)
          }
          that.earlyListData = that.earlyListData.concat([]);
          that.detailData = data.detailData
        }

        // console.log("数据已接收...");
        // console.log(received_msg);
      };

      ws.onclose = function () {
        // 关闭 websocket
        console.log("连接已关闭...");
      };
    } else {
      // 浏览器不支持 WebSocket
      console.log("您的浏览器不支持 WebSocket!");
    }
    }
  },
  watch: {
    searchByOrgId:function(){
      // console.log('333333333', this.searchByOrgId);
      this.getPersonList();
      // this.ws.close();
      this.websocketData(this);
    }

  },
  computed: {
    ...mapGetters(['showModal', 'routerLength', 'isIndex', 'searchByOrgId', 'tempNum'])
  },
  created() {
    //  this.AgetMediators();
    //  this.Agetdispatchers();
   },
  mounted() {
    let that = this
    this.setmodalHasBack(false);
    this.getPersonList();
    this.websocketData(that);
    
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../assets/scss/common.scss";
.index-wrapper {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  .index-content {
    width: 100%;
    height: calc(100% - .8rem);
    padding: .2rem;
  }
  .index-charts {
    width: 29%;
    height: 100%;
  }
  .index-flowchart {
    width: 36%;
    height: 100%;
    padding-left: .2rem;
    padding-right: .2rem;
  }
  .index-alarminfo {
    width: 35%;
    height: 100%;
  }
}
.right-sider {
  float: right;
  width: 100%;
  height: 100%;
  .sider-up {
    padding-top: .04rem;
    width: 100%;
    height: auto;
  }
  .up-row {
    padding-bottom: .16rem;
    width: 100%;
    height: auto;
    .label-wrap {
      position: relative;
      width: 30%;
      height: .48rem;
      .outer {
        position: absolute;
        z-index: 2;
        left: 0;
        top: 0;
        width: 70%;
        height: .4rem;
        line-height: .4rem;
        text-align: center;
        color: #fff;
        font-size: .18rem;
        background-color: $tit;
        white-space: nowrap;
        text-overflow: ellipsis;
        &::after {
          position: absolute;
          top: 0;
          right: -.28rem;
          width: .28rem;
          content: "";
          border-top: .2rem solid transparent;
          border-left: .14rem solid $tit;
          border-bottom: .2rem solid transparent;
        }
      }
      .inner {
        position: absolute;
        left: 0;
        bottom: 0;
        width: 78%;
        height: .4rem;
        background-color: #1b536c;
        &::after {
          position: absolute;
          top: 0;
          right: -.28rem;
          width: .28rem;
          content: "";
          border-top: .2rem solid transparent;
          border-left: .14rem solid #1b536c;
          border-bottom: .2rem solid transparent;
        }
      }
    }
    .up-content {
      width: 70%;
      height: .48rem;
      .up-item {
        padding-left: 6%;
        width: calc((100% - .18rem) / 2);
        height: 100%;
        text-align: left;
        background-color: $blue-lighter;
        .type {
          display: inline-block;
          padding-top: .2rem;
          height: 100%;
          line-height: 1;
          color: #fff;
          font-size: .14rem;
        }
        .num {
          display: inline-block;
          margin-right: .05rem;
          padding-top: .14rem;
          line-height: 1;
          font-size: .24rem;
          color: #e4ff32;
        }
        .unit {
          display: inline-block;
          padding-top: .2rem;
          height: 100%;
          line-height: 1;
          color: #fff;
          font-size: .14rem;
        }
      }
    }
    &.dispose .up-content .num {
      color: $blue;
    }
    &.dispose .up-content .up-item {
      cursor: pointer;
      &:hover {
        background-color: #42a0bd;
      }
    }
  }
  .sider-down {
    padding-top: .04rem;
    width: 100%;
    height: calc(100% - 1.32rem);
    background-color: $blue-dark;
  }
  .sider-down-inner {
    position: relative;
    padding: .2rem 0;
    width: 100%;
    height: calc(100% - .4rem);
  }
  .case-list-wrap {
    width: 100%;
    padding: .06rem .16rem 0;
    height: auto;
    .early-item {
      &:last-child {
        padding-bottom: 0;
      }
    }
  }
  .sider-scroll {
    width: 100%;
    height: 100%;
  }
}
.modal-router {
  position: fixed;
  left: 0;
  top: 0;
  z-index: 3;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.6);
  .animate-box {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 3;
    &.move-in {
      animation: moveIn 0.5s ease-in;
    }
  }
  @keyframes moveIn {
    0% {
      opacity: 0;
      top: -100%;
    }
    100% {
      opacity: 1;
      top: 0;
    }
  }
}
</style>
<style>
.index-wrapper .el-scrollbar__wrap {
  width: calc(100% + .17rem);
  height: calc(100% + .17rem);
}
.index-wrapper .el-scrollbar__bar.is-vertical {
  background-color: #062e4f;
}
.index-wrapper .el-scrollbar__bar.is-vertical > div {
  background-color: #40dbf8;
}
.scroll-deatil .el-scrollbar__wrap {
  width: calc(100% + .17rem);
  height: calc(100% + .17rem);
}
.scroll-deatil .el-scrollbar__bar.is-vertical {
  background-color: #062e4f;
}
.scroll-deatil .el-scrollbar__bar.is-vertical > div {
  background-color: #40dbf8;
}
.scroll-deatil .el-select > .el-input {
  height: 100%;
}
.scroll-deatil .el-select .el-input__inner {
  height: 100%;
  background-color: transparent;
  border: none;
  color: #fff;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  text-align: center;
}
.scroll-deatil .el-select .el-input__inner::placeholder {
  color: #fff;
}
.select-class.el-select-dropdown.el-popper .popper__arrow {
  display: none;
}
.list-select.select-class.el-select-dropdown.el-popper {
  margin: 0;
  background-color: #166f9a;
  border: .01rem solid #166f9a;
}
.select-class.el-select-dropdown.el-popper {
  margin: -.01rem;
  border-radius: 0;
  background-color: #166e98;
  border: .01rem solid #166e98;
}
.select-class .el-select-dropdown__item {
  color: #fff;
  text-align: center;
}
.select-class .el-select-dropdown__item.hover {
  background-color: transparent;
}
.select-class .el-select-dropdown__item:hover,
.select-class .el-select-dropdown__item.selected {
  background-color: #005777;
  color: #fff;
}
.list-select.select-class .el-select-dropdown__item:hover,
.list-select.select-class .el-select-dropdown__item.selected {
  background-color: #04547a;
  color: #fff;
}
.detail-content .el-select .el-input__inner {
  cursor: text;
}
.index-right .el-select .el-input__inner {
  cursor: pointer;
}
</style>
