<template>
  <div class="bar-wrap">
    <div :id="chartId"></div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import bar from "echarts";

export default {
  components: {},
  props: ["chartId", "chartData"],
  name: "bar-echart",
  data() {
    return {
      data: [],
      chart: null
    };
  },
  methods: {
    ...mapActions([]),
    setChart() {
      let nameArr = [];
      let valueArr = [];
      function sortData(data) {
        for (let i = 0; i < data.length - 1; i++) {
          for (let j = 0; j < data.length - i - 1; j++) {
            if (data[j].value > data[j + 1].value) {
              let temp = data[j];
              data[j] = data[j + 1];
              data[j + 1] = temp;
            }
          }
        }
        return data;
      }
      this.data = sortData(this.data);
      this.data.forEach(e => {
        nameArr.push(e.name);
        valueArr.push(e.value);
      });
      this.chart.setOption(
        {
          tooltip: {
            show: true
          },
          grid: {
            width: "auto",
            height: "80%",
            top: "10px",
            containLabel: true
          },
          yAxis: {
            type: "category",
            data: nameArr,
            axisLine: {
              lineStyle: {
                color: "#fff"
              }
            }
          },
          xAxis: {
            type: "value",
            axisLine: {
              show: false
            },
            axisLabel: {
              show: false
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: "#34546d",
                type: "dashed"
              }
            }
          },
          series: [
            {
              data: this.data,
              type: "bar",
              label: {
                show: true,
                position: "insideRight",
                offset: [40, 0],
                color: "#26edff",
                fontWeight: "lighter",
                fontSize: "14"
              },
              itemStyle: {
                normal: {
                  color: "#26edff"
                }
              }
            }
          ]
        },
        true
      );
    }
  },
  watch: {
    chartData(newVal) {
      this.data = newVal.list;
      this.totalCount = newVal.totalCount;
      // 异步加载数据
      // 填入数据
      this.setChart();
    }
  },
  computed: {
    ...mapGetters([])
  },
  created() {},
  mounted() {
    this.data = this.chartData;
    this.chart = bar.init(document.getElementById(this.chartId));
    // 显示标题，图例和空的坐标轴
    this.setChart();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../assets/scss/common.scss";
.bar-wrap {
  width: 100%;
  height: 100%;
  #bar-demo {
    width: 100%;
    height: 100%;
  }
}
</style>