<template>
  <div class="early-info clearfix" v-if="itemData.dispatchObj">
    <div class="pull-left info-item mr-4" :class="{'active': itemData.step >= 1?true:false }" @click="broadcastDetail(itemData.alarmInfo.properties.$id)">
      <div class="time" v-if="itemData.alarmInfo">{{ format(itemData.alarmInfo.dateModified) }}</div>
      <div class="first-inner">
        <div class="light-arrow-wrap being">
          <div class="arrow-tail iconfont iconarrow-tail"></div>
          <div class="arrow-tail iconfont iconarrow-tail"></div>
          <div class="arrow-tail iconfont iconarrow-tail"></div>
        </div>
        <div class="title">{{ itemData.alarmInfo.$displays.cause }}</div>
        <div class="info text-no-wrap">当事人：{{ itemData.alarmInfo.$displays.callerName }}</div>
        <div class="info text-no-wrap">{{ itemData.alarmInfo.$displays.eventAddress }}</div>
        <div class="info text-no-wrap">报警时间:{{ itemData.alarmInfo.$displays.callTime }}</div>
      </div>
    </div>
    <div class="pull-left info-item mr-4" :class="{'active': itemData.step >= 2?true:false }"  @click="callerDetail(itemData.dispatchObj)" v-if="itemData.dispatchObj">
      <div class="three-inner">
        <div class="light-arrow-wrap being">
          <div class="arrow-tail iconfont iconarrow-tail"></div>
          <div class="arrow-tail iconfont iconarrow-tail"></div>
          <div class="arrow-tail iconfont iconarrow-tail"></div>
        </div>
        <div
          class="office-img"
          :style="{'background-image': 'url('+fileUrl(itemData.dispatchObj.properties.portrait)+')'}"
        ></div>
        <div class="office-name text-no-wrap">{{ itemData.dispatchObj.$displays.name + '（调度员）'}}</div>
        <div class="num">{{ itemData.dispatchObj.dispatchNum }}件</div>
      </div>
    </div>
    <div class="pull-left info-item" :class="{'active': true }" @click="callerDetail(itemData.staffInfo)" v-if="itemData.staffInfo">
      <div class="three-inner">
        <div
          class="office-img"
          :style="{'background-image': 'url('+fileUrl(itemData.staffInfo.properties.portrait)+')'}"
        ></div>
        <div class="office-name text-no-wrap">{{ itemData.staffInfo.$displays.name + '（调解员）'}}</div>
        <div class="num">{{ itemData.staffInfo.handleNum }}件</div>
      </div>
    </div>
    <div class="pull-left info-item temp-block" v-else>
      <div class="three-inner">
        <div class="office-img">
          <div class="points-min-box clearfix">
            <div class="point-min pull-left"></div>
            <div class="point-min pull-left"></div>
            <div class="point-min pull-left"></div>
          </div>
        </div>
        <div class="office-name text-no-wrap">（调解员）</div>
        <div class="num">待分派</div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import apiService from "../api/index"
import util from "../util/index";

export default {
  components: {},
  name: "early-info",
  props: ["itemData"],
  data() {
    return {
    };
  },
  methods: {
    ...mapActions(['setSelfPersonType','AgetMediatorDetailInfo','setmodalHasBack','AgetdispatcherDetailInfo', 'setModal','setCaseId']),
    orgUrl(typeId){
      //司法局
      //司法所
      //调委会
      if('255afee102374b60b770de3b601f0f0b' === typeId){
        return './images/jthyjf.png'
      }else if('98e06bc12e814282bd705b850723c6ed' === typeId){
        return './images/yljf.png'
      }else {
        return './images/lljf.png'
      }
    },
    format(dateStr){
      return util.format(new Date(dateStr), 'yyyy-MM-dd HH:mm');
    },
    getNum(user){
      //公职人员
      if('8f3cd2165cb540b189ce86c143fa3afd' === user.properties.$typeId){
        return user.properties.dispatchNum
        //调解员
      }else if('9b3dd1fc6e59471d89ec814aadfe9cce' === user.properties.$typeId){
        return user.properties.handleNum
      }
      return 0;
    },

    fileUrl(id) {
      if (id) {
        return apiService.fileUrl + id + "/view?thumb=200x200";
      }
      return "./images/portrait.jpg";
    },
    callerDetail(item) {
      // console.log(item.properties.$typeId)
      if(item.properties.$typeId == "8f3cd2165cb540b189ce86c143fa3afd") {
        this.setSelfPersonType('dispatcher');
        this.AgetdispatcherDetailInfo(item.properties.$id);
      }else {
        this.setSelfPersonType('mediator');
        this.AgetMediatorDetailInfo(item.properties.$id);
      }
      this.setModal(true);
      this.$router.push({ path: '/mediatorDetail' });
    },
    broadcastDetail(item) {
      this.setModal(true);
      this.setCaseId(item).then(() =>{
        this.$router.push({ path: '/caseDetail'});
      });
    },

  },
  watch: {},
  computed: {
    ...mapGetters([])
  },
  created() {},
  mounted() {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->

<style scoped lang="scss" rel="stylesheet/scss">
@import "../assets/scss/common.scss";
.early-info {
  // margin-top: .06rem;
  margin-bottom: .22rem;  
  position: relative;
  width: 100%;
  border-top: .03rem solid #0682cb;
  height: auto;
  .mr-4 {
    margin-right: .04rem;
  }
  .info-item {
    position: relative;
    cursor: pointer;
    width: calc((100% - .1rem) / 3);
    height: 1.45rem;
    background-color: $blue-light;
    &.temp-block {
      cursor: default;
      .points-min-box {
        position: absolute;
        left: 50%;
        top: 50%;
        min-width: .26rem;
        height: .06rem;
        transform: translate(-50%, -50%);
      }
      .point-min {
        width: .06rem;
        height: .06rem;
        border-radius: 50%;
        margin-right: .04rem;
        background-color: #629cc4;
        &:last-of-type {
          margin-right: 0;
        }
      }
      .office-img {
        position: relative;
        background-color: #629cc4;
      }
      .three-inner .num,
      .three-inner .text-no-wrap {
        color: #629cc4;
      }
      &:nth-child(2n + 1):hover {
        background-color: #043556;
        color: #629cc4;
        .first-inner .info {
          color: #629cc4;
        }
      }
    }
    &.active {
      background-color: $blue-lighter;
    }
    .first-inner {
      padding-left: .15rem;
      padding-right: .05rem;
      text-align: left;
      font-weight: lighter;
      .title {
        padding-top: .27rem;
        padding-bottom: .03rem;
        line-height: 1;
        font-size: .2rem;
        color: #fff;
      }
      .info {
        padding: .06rem 0 0;
        color: #e8e8e8;
        font-size: .16rem;
      }
    }
    .three-inner {
      font-size: .16rem;
      color: #fff;
      padding-top: .2rem;
      .office-img {
        margin: 0 auto .1rem;
        width: .48rem;
        height: .48rem;
        border-radius: 50%;
        background-color: #206c9c;
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center;
      }
      .icon-office {
        font-size: .44rem;
        color: #1ebfd6;
      }
      .office-name {
        font-size: .16rem;
        color: #fff;
      }
      .num {
        font-size: .16rem;
        color: #fff;
      }
    }
  }
  .info-item:nth-child(2n + 1):hover {
    background-color: $tit;
    color: #fff;
  }
  .info-item:nth-child(2n + 1):hover {
    background-color: $tit;
    color: #fff;
    .first-inner .info {
      color: #fff;
    }
  }
  .time {
    position: absolute;
    top: -.08rem;
    left: 0;
    width: 70%;
    height: .24rem;
    line-height: .24rem;
    color: #fff;
    font-size: .12rem;
    font-weight: lighter;
    background-color: #0682cb;
    white-space: nowrap;
    text-overflow: ellipsis;
    &::after {
      position: absolute;
      top: .02rem;
      right: -.04rem;
      width: .08rem;
      height: .08rem;
      content: "";
      border: .04rem solid transparent;
      border-right: .04rem solid #0682cb;
      transform: rotate(-45deg);
    }
  }
  .light-arrow-wrap {
    position: absolute;
    right: .08rem;
    top: 0;
    width: .32rem;
    height: .32rem;
    text-align: center;
    transform: rotate(-90deg);
    overflow: hidden;
    .arrow-tail {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      color: $blue;
      font-size: .12rem;
      &:nth-child(1) {
        top: 0;
        opacity: 1;
      }
      &:nth-child(2) {
        opacity: 0.8;
        top: .1rem;
      }
      &:nth-child(3) {
        opacity: 0.5;
        top: .2rem;
      }
      &:nth-child(4) {
        opacity: 0.2;
        top: .3rem;
      }
    }
    &.being .arrow-tail {
      &:nth-child(3) {
        animation: zebra1 1.6s linear infinite;
      }
      &:nth-child(2) {
        animation: zebra2 1.6s linear infinite;
      }
      &:nth-child(1) {
        animation: zebra3 1.6s linear infinite;
      }
    }
    @keyframes zebra1 {
      0% {
        opacity: 1;
      }
      25% {
        opacity: 0.8;
      }
      50% {
        opacity: 0.5;
      }
      75% {
        opacity: 0.2;
      }
      100% {
        opacity: 1;
      }
    }
    @keyframes zebra2 {
      0% {
        opacity: 0.8;
      }
      25% {
        opacity: 0.5;
      }
      50% {
        opacity: 0.2;
      }
      75% {
        opacity: 1;
      }
      100% {
        opacity: 0.8;
      }
    }
    @keyframes zebra3 {
      0% {
        opacity: 0.5;
      }
      25% {
        opacity: 0.2;
      }
      50% {
        opacity: 1;
      }
      75% {
        opacity: 0.8;
      }
      100% {
        opacity: 0.5;
      }
    }
  }
}
</style>

