<template>
  <div class="header-wrap">
    <div class="today-state">
      <div class="today-state-box clearfix">
        <div class="pull-left today-day">{{day}}</div>
        <div class="pull-left today-week">{{week}}</div>
        <div class="pull-left today-time">{{time}}</div>
      </div>
      <div class="pull-left weatherPic">
        <svg class="weatherIcon iconfont" aria-hidden="true">
          <use xlink:href="#icontianqi-zhongxue" v-if="currentWeather === '中雪'"></use>
          <use xlink:href="#icontianqi-zhongyu" v-else-if="currentWeather === '中雨'"></use>
          <use xlink:href="#icontianqi-yujiaxue" v-else-if="currentWeather === '雨夹雪'"></use>
          <use xlink:href="#icontianqi-xuezhuanqing" v-else-if="currentWeather === '雪转晴'"></use>
          <use xlink:href="#icontianqi-xiaoyuzhuanqing" v-else-if="currentWeather === '小雨转晴'"></use>
          <use xlink:href="#icontianqi-xiaoxue" v-else-if="currentWeather === '小雪'"></use>
          <use xlink:href="#icontianqi-xiaoyu" v-else-if="currentWeather === '小雨'"></use>
          <use xlink:href="#icontianqi-wumai" v-else-if="currentWeather === '雾霾'"></use>
          <use xlink:href="#icontianqi-daxue" v-else-if="currentWeather === '大雪'"></use>
          <use xlink:href="#icontianqi-shuang" v-else-if="currentWeather === '霜'"></use>
          <use xlink:href="#icontianqi-qing" v-else-if="currentWeather === '晴'"></use>
          <use xlink:href="#icontianqi-duoyun" v-else-if="currentWeather === '多云'"></use>
          <use xlink:href="#icontianqi-baoyu" v-else-if="currentWeather === '暴雨'"></use>
          <use xlink:href="#icontianqi-dayu" v-else-if="currentWeather === '大雨'"></use>
          <use xlink:href="#icontianqi-leiyu" v-else-if="currentWeather === '雷雨'"></use>
          <use xlink:href="#icontianqi-bingbao" v-else-if="currentWeather === '冰雹'"></use>
          <use xlink:href="#icontianqi-yintian" v-else></use>
        </svg>
      </div>
      <div class="pull-left today-weather">{{temperature}}℃</div>
    </div>
    <div class="title-name"></div>
    <div class="left-logo">
      <div class="pull-left select-box">
        <div class="current-box" :class="{down:isdown}" @click="isdown=!isdown">
          <div class="current-content">{{currentName}}</div>
        </div>
        <div class="list-area" v-show="isdown" @mouseleave="hidearea" @mouseenter="enterarea">
          <el-scrollbar class="list-scroll">
            <div
              class="list-area-item"
              :class="[item.type === 'active'?'active': '']"
              v-for="(item, index) in justiceBureaus"
              :key="item.id"
              @mouseover="choosearea(item)"
              @click="chooseArea(item)"
            >
              {{item["properties.name"]}}
              <i class="iconfont iconfanhui arrow-icon" v-if="index !== 0"></i>
            </div>
          </el-scrollbar>
        </div>
        <div
          class="list-lawyer"
          v-if="lawyer.length > 0"
          v-show="isdown"
          @mouseleave="hidelawyer"
          @mouseenter="enterlawyer"
        >
          <el-scrollbar class="list-scroll">
            <div
              class="list-area-item"
              :class="[item.type === 'active'?'active': '']"
              v-for="item in lawyer"
              :key="item.id"
              @click="clicklawyer(item)"
            >{{item["properties.name"]}}</div>
          </el-scrollbar>
        </div>
      </div>
      <a class="content-logo" href="javascript:;">
        <div class="pull-left header-logo"></div>
        <div class="pull-left logo-content">智慧调解</div>
      </a>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import { AMapManager } from 'vue-amap';
import { lazyAMapApiLoaderInstance } from 'vue-amap';
import '../assets/fonts/iconfont.js';

export default {
  components: {
  },
  name: "headerVue",
  data() {
    return {
      weatherObj: null,
      currentWeather: '',
      temperature: 0,
      day: '',
      time: '',
      week: '',
      isdown: false,
      areaList: [
        {
          id: 0,
          type: 'active',
          name: '上海市司法局',
          children: []
        },
        {
          id: 1,
          type: '',
          name: '黄浦区司法局1',
          children: [
            {
              id: 11,
              type: '',
              name: '司法所1'
            },
            {
              id: 12,
              type: '',
              name: '司法所2'
            },
            {
              id: 13,
              type: '',
              name: '司法所3'
            },
            {
              id: 14,
              type: '',
              name: '司法所4'
            }
          ]
        },
        {
          id: 2,
          type: '',
          name: '黄浦区司法局2',
          children: [
            {
              id: 21,
              type: '',
              name: '司法所21'
            },
            {
              id: 22,
              type: '',
              name: '司法所22'
            },
            {
              id: 23,
              type: '',
              name: '司法所23'
            },
            {
              id: 24,
              type: '',
              name: '司法所24'
            }
          ]
        }
      ],
      lawyer: [],
      islawyer: false,
      isarea: false,
      currentName: '上海司法局'
    }
  },
  methods: {
    ...mapActions([
      "setJusticeBureaus",
      "updateSearchByOrgId",
      "AgetMediators",
      "clearMediatorsList",
      "Agetdispatchers",
      "setShowSendAjax",
      'changeIndexDataArr'
    ]),
    choosearea(item) {
      this.lawyer = item.justiceOffices;
    },
    hidelawyer() {
      this.lawyer = [];
      this.islawyer = false;
      setTimeout(() => {
        if (!this.isarea) {
          this.isdown = false;
          this.lawyer = [];
        }
      }, 100)
    },
    hidearea() {
      this.isarea = false;
      if (this.lawyer.length <= 0) {
        this.isdown = false;
      } else {
        setTimeout(() => {
          if (!this.islawyer) {
            this.isdown = false;
            this.lawyer = [];
          }
        }, 100)
      }
    },
    enterlawyer() {
      this.islawyer = true;
    },
    enterarea() {
      this.isarea = true;
    },
    clicklawyer(item) {
      this.setShowSendAjax('index');
      this.changeIndexDataArr();
      let fromArr = this.lawyer;
      this.updateSearchByOrgId(item.id);
      this.justiceBureaus.forEach(e => {
        e.type = '';
        if (e.justiceOffices === fromArr) {
          e.type = 'active';
          fromArr.forEach(ele => {
            ele.type = '';
            if (ele === item) {
              ele.type = 'active';
              this.currentName = item["properties.name"];
            }
          })
        } else {
          e.justiceOffices.forEach(ele => {
            ele.type = '';
          })
        }
      })
      
      setTimeout(() => {
        this.isarea = false;
        this.isdown = false;
        this.islawyer = false;
      }, 100);
    },
    chooseArea(item) {
      this.setShowSendAjax('index');
      this.changeIndexDataArr();
      this.currentName = item["properties.name"];
      this.updateSearchByOrgId(item.id);
      this.justiceBureaus.forEach(e => {
        e.type = '';
        e.justiceOffices.forEach(ele => {
          ele.type = '';
        })
        if(e === item) {
          e.type = 'active';
        }
      })
      this.hidearea();
    }
  },
  watch: {
    searchByOrgId:function() {
      this.clearMediatorsList();
      
    }
  },
  computed: {
    ...mapGetters(["justiceBureaus",'searchByOrgId'])
  },
  created() {
    let that = this;
    lazyAMapApiLoaderInstance.load().then(() => {
      AMap.plugin('AMap.Weather', function () {
        let weather = new AMap.Weather();
        weather.getLive('上海市', function (err, data) {
          that.temperature = data.temperature;
          that.currentWeather = data.weather;
        });
        setInterval(() => {
          weather.getLive('上海市', function (err, data) {
            that.temperature = data.temperature;
            that.currentWeather = data.weather;
          });
        }, 60000);
      })
    });
    let date = new Date();
    this.day = date.getFullYear() + '-' + setTime(date.getMonth() + 1) + '-' + setTime(date.getDate());
    this.time = date.getHours() + ':' + setTime(date.getMinutes()) + ':' + setTime(date.getSeconds());
    function setTime(num) {
      if (num < 10) {
        num = '0' + num;
      }
      return num;
    }
    function setweek(weekNum) {
      let week = ''
      switch (date.getDay()) {
        case 0:
          week = '星期天'
          break;
        case 1:
          week = '星期一'
          break;
        case 2:
          week = '星期二'
          break;
        case 3:
          week = '星期三'
          break;
        case 4:
          week = '星期四'
          break;
        case 5:
          week = '星期五'
          break;
        case 6:
          week = '星期六'
          break;
      }
      return week;
    }
    this.week = setweek(date.getDay());
    setInterval(() => {
      let date = new Date();
      this.time = date.getHours() + ':' + setTime(date.getMinutes()) + ':' + setTime(date.getSeconds());
      if (this.time === '00:00:00') {
        this.week = setweek(date.getDay());
        this.day = date.getFullYear() + '-' + setTime(date.getMonth() + 1) + '-' + setTime(date.getDate());
      }
    }, 1000);
  },
  mounted() {
    this.setJusticeBureaus();
    setTimeout(()=>{
      // console.log("机构:" + this.justiceBureaus[0]["properties.name"]);

    },1000);
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../assets/scss/common.scss";
.header-wrap {
  position: relative;
  width: 100%;
  height: .8rem;
  // background: linear-gradient(to top, #042c4a, #001f3e, #001f3e, #042c4a);
  .title-name {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    max-width: 6.75rem;
    height: .8rem;
    background: url("../../public/images/index_title.png") no-repeat
      center/cover;
    z-index: 2;
  }
  .left-logo {
    position: absolute;
    right: .45rem;
    top: 0;
    line-height: .8rem;
  }
  .content-logo {
    color: $white;
    font-size: .2rem;
    line-height: .8rem;
  }
  .header-logo {
    width: .36rem;
    height: .8rem;
    background: url("../../public/images/header-logo.png") no-repeat
      center/contain;
  }
  .logo-content {
    padding-left: .13rem;
  }
  .today-state {
    position: absolute;
    left: .2rem;
    top: 0;
    padding-top: .2rem;
    line-height: .4rem;
    color: $white;
    font-size: .16rem;
  }
  .today-state-box {
    float: left;
    padding: 0 .2rem;
    margin-right: .2rem;
    background-image: linear-gradient(
      to right,
      rgba(66, 160, 189, 0.5),
      rgba(64, 115, 207, 0.5)
    );
  }
  .today-day {
    padding-right: .2rem;
  }
  .today-week {
    padding-right: .2rem;
  }
  .today-weather {
    padding-right: .2rem;
  }
  .select-box {
    position: relative;
    height: .8rem;
    margin-right: .3rem;
    padding-top: .2rem;
    cursor: pointer;
    .current-box {
      position: relative;
      width: 2rem;
      height: .4rem;
      padding-left: .1rem;
      padding-right: .3rem;
      border: .01rem solid #42a0bd;
      box-sizing: border-box;
      background-color: #011f36;
      &::after {
        position: absolute;
        right: .15rem;
        top: .16rem;
        border: .06rem solid transparent;
        border-top-color: #42a0bd;
        content: "";
        transform-origin: center .03rem;
      }
      &.down::after {
        transform: rotate(180deg);
      }
    }
    .current-content {
      width: 100%;
      font-size: .16rem;
      line-height: .38rem;
      color: #42a0bd;
      text-align: left;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
    .list-area {
      position: absolute;
      left: 0;
      top: .6rem;
      z-index: 3;
      width: 100%;
      height: 3.84rem;
      margin: 0;
      padding: 0;
    }
    .list-area-item {
      position: relative;
      list-style: none;
      width: 100%;
      height: .48rem;
      padding-left: .15rem;
      line-height: .48rem;
      font-size: .16rem;
      color: #fff;
      font-weight: lighter;
      background-color: #034165;
      text-align: left;
      &.active {
        background-color: #42a0bd;
      }
      &:hover {
        background-color: darken($color: #42a0bd, $amount: 5);
      }
      .arrow-icon {
        position: absolute;
        right: .15rem;
        top: 0;
        color: #fff;
        transform: rotate(180deg);
      }
    }
    .list-lawyer {
      position: absolute;
      left: 2rem;
      top: .6rem;
      z-index: 2;
      width: 100%;
      height: 3.84rem;
      background-color: #00314d;
    }
    .list-scroll {
      width: 100%;
      height: 100%;
      background-color: #034165;
    }
  }
  .weatherPic {
    position: relative;
    width: .4rem;
    height: .4rem;
    font-size: .18rem;
    .weatherIcon {
      position: absolute;
      left: .05rem;
      top: .05rem;
      width: .3rem;
      height: .3rem;
      vertical-align: middle;
      overflow: hidden;
      font-size: .18rem;
      fill: currentColor;
    }
  }
}
</style>