<template>
  <div class="mediator-list">
    <modal-header :titleName="titleName"></modal-header>
    <div class="mediator-content clearfix" v-if="personType === 'mediator'">
      <!-- 左侧导航栏开始 -->
      <div class="mediator-content-left pull-left">
        <el-scrollbar class="navbar-content-scroll">
          <ul class="navbar-content-box">
            <li class="navbar-content-list" v-for="(item, index) in justiceBureausNew" :key="index">
              <a href="javascript:;" class="navbar-link" @click="changeNavbar(index)">
                <i
                  class="navbar-icon"
                  :class="{'navbarActive': navbarIndex === index}"
                  v-show="item.justiceOffices.length > 0"
                ></i>
                {{item["properties.name"]}}
              </a>
              <div class="navbar-item-content" v-show="navbarIndex === index">
                <a
                  href="javascript:;"
                  v-for="(justiceOffice, i) in item.justiceOffices"
                  :key="i"
                  class="navbar-item-link"
                  :class="{'active':justiceOffice.type === 'active' }"
                  @click="chooseChild(justiceOffice)"
                >{{justiceOffice["properties.name"]}}</a>
              </div>
            </li>
          </ul>
        </el-scrollbar>
      </div>
      <!-- 左侧导航栏结束 -->
      <div class="mediator-content-right pull-right">
        <div class="pull-right list-count">总计：&nbsp;{{outerMediatorNum}}人</div>
        <div class="select-name-outer">
          <input
            class="select-name-inner"
            type="text"
            placeholder="请输入人员姓名搜索"
            v-model="searchmediator"
          >
          <div class="iconfont iconsousuo icon-select-btn"></div>
        </div>
        <el-scrollbar class="scroll-deatil" ref="mediatorbar">
          <div class="detail-content-box">
            <!-- <div class="peopleList-nav clearfix">
              <el-select
                class="nav-area"
                v-model="outerareaValue"
                placeholder="区域选择"
                popper-class="select-class list-select"
              >
                <el-option
                  v-for="(item, index) in outerbureauData"
                  :key="index"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
              <el-select
                class="nav-place"
                v-model="outerplaceValue"
                placeholder="全部司法所"
                popper-class="select-class list-select"
              >
                <el-option
                  v-for="(item, index) in outerofficeData"
                  :key="index"
                  :label="item.name"
                  :value="item.id"
                ></el-option>
              </el-select>
            </div>-->
            <ul class="peopleList-content-box clearfix">
              <li
                class="peopleList-box"
                v-for="(item, index) in mediators"
                :key="index"
                @click="callerDetail(item)"
              >
                <div
                  class="peopleList-img"
                  :style="{'background-image': 'url(' + fileUrl(item.properties.portrait) + ')'}"
                ></div>
                <div class="peopleList-name">{{item.properties.name}}</div>
                <div class="peopleList-place">{{item.$displays.practiceOrg}}</div>
              </li>
            </ul>
          </div>
        </el-scrollbar>
      </div>
    </div>
    <div class="mediator-content" v-else>
      <!-- 左侧导航栏开始 -->
      <div class="mediator-content-left pull-left">
        <el-scrollbar class="navbar-content-scroll">
          <ul class="navbar-content-box">
            <li class="navbar-content-list" v-for="(item, index) in justiceBureausNew" :key="index">
              <a href="javascript:;" class="navbar-link" @click="changeNavbar(index)">
                <i
                  class="navbar-icon"
                  :class="{'navbarActive': navbarIndex === index}"
                  v-show="item.justiceOffices.length > 0"
                ></i>
                {{item["properties.name"]}}
              </a>
              <div class="navbar-item-content" v-show="navbarIndex === index">
                <a
                  href="javascript:;"
                  v-for="(justiceOffice, index) in item.justiceOffices"
                  :key="index"
                  class="navbar-item-link"
                  :class="[justiceOffice.type === 'active' ? 'active' : '']"
                  @click="chooseChild(justiceOffice)"
                >{{justiceOffice["properties.name"]}}</a>
              </div>
            </li>
          </ul>
        </el-scrollbar>
      </div>
      <!-- 左侧导航栏结束 -->
      <div class="mediator-content-right pull-right">
        <div class="pull-right list-count">总计：&nbsp;{{outerDispatcherNum}}人</div>
        <div class="select-name-outer">
          <input
            class="select-name-inner"
            type="text"
            placeholder="请输入人员姓名搜索"
            v-model="searchmediator"
          >
          <div class="iconfont iconsousuo icon-select-btn"></div>
        </div>
        <el-scrollbar class="scroll-deatil" ref="dispatcherbar">
          <div class="detail-content-box">
            <!-- <div class="peopleList-nav clearfix">
            <el-select
              class="nav-area"
              v-model="outerareaValue"
              placeholder="区域选择"
              popper-class="select-class list-select"
            >
              <el-option
                v-for="(item, index) in outerbureauData"
                :key="index"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
            <el-select
              class="nav-place"
              v-model="outerplaceValue"
              placeholder="全部司法所"
              popper-class="select-class list-select"
            >
              <el-option
                v-for="(item, index) in outerofficeData"
                :key="index"
                :label="item.name"
                :value="item.id"
              ></el-option>
            </el-select>
            </div>-->
            <ul class="peopleList-content-box clearfix">
              <li
                class="peopleList-box"
                v-for="(item, index) in dispatchers"
                :key="index"
                @click="callerDetail(item)"
              >
                <div
                  class="peopleList-img"
                  :style="{'background-image': 'url(' + fileUrl(item.properties.portrait) + ')'}"
                ></div>
                <div class="peopleList-name">{{item.properties.name}}</div>
                <div class="peopleList-place">{{item.$displays.practiceOrg}}</div>
              </li>
            </ul>
          </div>
        </el-scrollbar>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import modalHeader from "@/components/modal-header.vue";
import api from "@/api/index.js";

export default {
  components: {
    modalHeader
  },
  name: "caseList",
  data() {
    return {
      titleName: "调解员",
      currentId: '',
      justiceBureausNew: [{
        type: '',
        id: '',
        justiceOffices: [{
          type: ''
        }]
      }],
      outerareaValue: "",
      outerplaceValue: "",
      searchmediator: "",
      searchById: "",
      navbarIndex: -1,
      navbarLinkList: [
        { name: '宝山区司法局' },
        { name: '宝山区司法局' },
        { name: '宝山区司法局' }
      ],
      outerbureauData: [
        {
          id: 0,
          type: 'active',
          name: '上海市司法局',
          children: []
        },
        {
          id: 1,
          type: '',
          name: '黄浦区司法局1',
          children: [
            {
              id: 11,
              type: '',
              name: '司法所1'
            },
            {
              id: 12,
              type: '',
              name: '司法所2'
            },
            {
              id: 13,
              type: '',
              name: '司法所3'
            },
            {
              id: 14,
              type: '',
              name: '司法所4'
            }
          ]
        },
        {
          id: 2,
          type: '',
          name: '黄浦区司法局2',
          children: [
            {
              id: 21,
              type: '',
              name: '司法所21'
            },
            {
              id: 22,
              type: '',
              name: '司法所22'
            },
            {
              id: 23,
              type: '',
              name: '司法所23'
            },
            {
              id: 24,
              type: '',
              name: '司法所24'
            }
          ]
        }
      ],
      getListLoad: true,
      getListLoad2: true
    };
  },
  methods: {
    ...mapActions([
      "AgetSelectData",
      "setPageNum",
      "AgetMediators",
      "clearMediatorsList",
      "ASetSelectType",
      "changeOfficeData",
      "setmodalHasBack",
      "AgetMediatorDetailInfo",
      "Agetdispatchers",
      "setSelfPersonType",
      "AgetdispatcherDetailInfo",
      "setIsIndex",
      'setClearPerson'
    ]),
    // 点击切换navbar的index
    changeNavbar(index) {
      if (this.navbarIndex === index) {
        this.navbarIndex = -1
        return
      } else {
        this.navbarIndex = index;
        this.navbarLinkList = this.justiceBureausNew[index].justiceOffices;
        this.justiceBureausNew.forEach(e => {
          e.type = '';
          if (!e.justiceOffices) {
            e.justiceOffices = [];
          }
          if (e.justiceOffices.length > 0) {
            e.justiceOffices.forEach(ele => {
              ele.type = '';
            })
          }
        })
        this.justiceBureausNew[index].type = 'active';
        this.clearMediatorsList();
        this.setPageNum("clear");
        this.searchById = this.justiceBureausNew[index].id;
        this.getList('', this.justiceBureausNew[index].id);
        this.getListDispatcher('', this.justiceBureausNew[index].id);
      }

    },
    chooseChild(item) {
      // this.currentId = item.id
      this.justiceBureausNew.forEach(e => {
        e.type = '';
        if (!e.justiceOffices) {
          e.justiceOffices = [];
        }
        if (e.justiceOffices.length > 0) {
          e.justiceOffices.forEach(ele => {
            ele.type = '';
          })
        }
        if (e.justiceOffices === this.navbarLinkList) {
          e.type = 'active';
          e.justiceOffices.forEach(ele => {
            ele.type = '';
            if (ele === item) {
              ele.type = 'active';
            }
          })
        }
      })
      // 返回一个新数组，监听对象
      this.justiceBureausNew = this.justiceBureausNew.concat([]);
      this.clearMediatorsList();
      this.setPageNum("clear");
      this.searchById = item.id;
      this.getList('', item.id);
      this.getListDispatcher('', item.id);

    },
    fileUrl(id) {
      if (id) {
        return api.fileUrl + id + "/view?thumb=200x200";
      }
      return "./images/portrait.jpg";
    },
    getList(val, orgidVal) {
      let that = this;
      let searchval = val || "";
      let orgid = orgidVal || this.searchById;
      let params = { "name": searchval, "orgId": orgid };
      this.AgetMediators(params);
      this.$nextTick(() => {
        this.scrollmediatorList = this.$refs["mediatorbar"].$refs["wrap"];
        let height =
          that.scrollmediatorList.scrollHeight -
          that.scrollmediatorList.scrollTop;
        this.scrollmediatorList.onscroll = function () {
          if (
            that.scrollmediatorList.scrollHeight - height - that.scrollmediatorList.scrollTop < 5
          ) {
            if (that.getListLoad) {
              that.getListLoad = false;
              that.setPageNum('add');
              that.AgetMediators(params);
            }
          } else {
            that.getListLoad = true;
          }
        };
      });
    },
    callerDetail(item) {
      // console.log(item.properties.$typeId);
      if (item.properties.$typeId == "8f3cd2165cb540b189ce86c143fa3afd") {
        this.setSelfPersonType("dispatcher");
        this.AgetdispatcherDetailInfo(item.properties.$id);
      } else {
        this.setSelfPersonType("mediator");
        this.AgetMediatorDetailInfo(item.properties.$id);
      }
      this.setmodalHasBack(true);
      this.setClearPerson();
      this.$router.push({ path: "/mediatorDetail" });
    },
    getListDispatcher(val, orgidVal) {
      let that = this;
      let searchval = val || "";
      let orgid = orgidVal || this.searchById;
      let params = { "name": searchval, "orgId": orgid };
      this.Agetdispatchers(params);
      this.$nextTick(() => {
        this.scrolldispatcherList = this.$refs["dispatcherbar"].$refs["wrap"];
        let height =
          that.scrolldispatcherList.scrollHeight -
          that.scrolldispatcherList.scrollTop;
        this.scrolldispatcherList.onscroll = function () {
          if (
            that.scrolldispatcherList.scrollHeight - height - that.scrolldispatcherList.scrollTop < 5
          ) {
            if (that.getListLoad) {
              that.getListLoad = false;
              that.setPageNum('add');
              that.Agetdispatchers(params);
            }
          } else {
            that.getListLoad = true;
          }
        };
      });
    }
  },
  watch: {
    searchmediator(newVal) {
      let that = this;
      this.setPageNum("clear");
      this.clearMediatorsList();
      if (this.personType === "mediator") {
        this.getList(newVal);
      } else {
        this.getListDispatcher(newVal);
      }
    },
    outerareaValue(newVal) {
      let val = {};
      if (newVal === "-1") {
        val = { type: "", val: "" };
      } else {
        val = { type: "area", val: newVal };
      }
      this.setPageNum("clear");
      this.clearMediatorsList();
      this.ASetSelectType(val);
      if (this.personType === "mediator") {
        this.getList();
      } else {
        this.getListDispatcher();
      }
      this.outerplaceValue = '-1'
      this.changeOfficeData(val);
    },
    outerplaceValue(newVal) {
      let val = {};
      if (newVal === "-1") {
        if (this.outerareaValue.length > 0 && this.outerareaValue != "-1") {
          val = { type: "area", val: this.outerareaValue };
        } else {
          val = { type: "", val: "" };
        }
      } else {
        val = { type: "place", val: newVal };
      }
      this.setPageNum("clear");
      this.clearMediatorsList();
      this.ASetSelectType(val);
      if (this.personType === "mediator") {
        this.getList();
      } else {
        this.getListDispatcher();
      }
    },
    searchByOrgId: function () {

    }
  },
  computed: {
    ...mapGetters([
      "outerofficeData",
      "mediators",
      "outerMediatorNum",
      "personType",
      "outerDispatcherNum",
      "dispatchers",
      "justiceBureaus",
      "searchByOrgId"
    ])
  },
  created() {
    this.searchById = this.searchByOrgId;
    setTimeout(() => {
      this.setIsIndex(false);
    }, 500);
    this.setPageNum("clear");
    if (this.personType === "mediator") {
      this.titleName = "调解员";
      this.getList();
    } else {
      this.titleName = "调度员";
      this.getListDispatcher();
    }
  },
  mounted() {
    this.clearMediatorsList();
    let that = this;
    this.AgetSelectData();
    let justiceBureausNew = this.justiceBureaus;
    this.justiceBureausNew = justiceBureausNew;
    for (let val in justiceBureausNew) {
      if (justiceBureausNew[val].type === 'active') {
        this.navbarIndex = parseInt(val);
        this.navbarLinkList = justiceBureausNew[val].justiceOffices;
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../../assets/scss/common.scss";
.mediator-list {
  position: absolute;
  top: 1.26rem;
  left: 10%;
  z-index: 3;
  width: 80%;
  font-size: .16rem;
  height: calc(100% - 2.52rem);
  background-color: #032b4a;
  .mediator-content {
    width: 100%;
    height: calc(100% - .64rem);
    // padding: .24rem .12rem .3rem 0;
    .mediator-content-left {
      width: 18%;
      height: 100%;
      background-color: #01243f;
    }
    .mediator-content-right {
      width: 82%;
      height: 100%;
      padding: .24rem .12rem .3rem .3rem;
    }
  }
  .scroll-deatil {
    width: 100%;
    height: calc(100% - .52rem);
  }
  .detail-content-box {
    width: 100%;
    padding-right: .18rem;
  }
  .list-count {
    padding-top: .2rem;
    padding-right: .18rem;
    font-weight: lighter;
    font-size: .18rem;
    color: $white;
  }
  .select-name-outer {
    position: relative;
    float: left;
    width: 36%;
    height: .64rem;
    line-height: .36rem;
    padding-bottom: .3rem;
    z-index: 5;
    .select-name-inner {
      display: block;
      width: 100%;
      height: 100%;
      padding: 0 .35rem 0 .22rem;
      margin: 0;
      border: none;
      font-size: .16rem;
      font-weight: lighter;
      color: $white;
      background-color: #08406e;
      &::placeholder {
        color: #9abedb;
      }
      &:focus {
        outline: none;
      }
    }
    .icon-select-btn {
      position: absolute;
      right: 0;
      top: 0;
      padding-right: .18rem;
      line-height: .34rem;
      font-size: .22rem;
      color: #bcedff;
    }
  }
  .peopleList-nav {
    position: absolute;
    left: 0;
    top: 0;
    width: calc(100% - .18rem);
    height: .48rem;
    padding-right: .23rem;
    line-height: .48rem;
    color: $white;
    font-size: .16rem;
    background-color: #1c6c9b;
    .nav-area {
      position: absolute;
      top: 0;
      left: 0;
      width: 1.2rem;
      height: .48rem;
    }
    .nav-place {
      position: absolute;
      top: 0;
      left: 1.23rem;
      width: 1.5rem;
      height: .48rem;
    }
  }
  .peopleList-content-box {
    width: 100%;
    margin: 0;
    padding: 0;
    list-style: none;
    .peopleList-box {
      list-style: none;
      float: left;
      width: calc((100% - .64rem) / 4);
      height: 2.1rem;
      padding: .2rem .2rem 0;
      margin-right: .2rem;
      margin-bottom: .2rem;
      border: .01rem solid #166f9a;
      cursor: pointer;
      &:nth-of-type(4n) {
        margin-right: 0;
      }
      &:hover {
        background-color: #166f9a;
      }
    }
    .peopleList-img {
      margin: 0 auto;
      width: .8rem;
      height: .8rem;
      border-radius: 50%;
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
    }
    .peopleList-name {
      width: 100%;
      padding-top: .15rem;
      line-height: .3rem;
      color: $white;
      font-size: .2rem;
      text-align: center;
    }
    .peopleList-place {
      width: 100%;
      line-height: .25rem;
      color: $white;
      font-size: .18rem;
      text-align: center;
      overflow: hidden;
      font-weight: lighter;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
    }
  }
  // 左侧导航栏样式
  .navbar-main-title {
    padding-left: .3rem;
    font-size: .18rem;
    color: $white;
    line-height: .48rem;
    text-align: left;
  }
  .navbar-content-scroll {
    height: 100%;
  }
  .navbar-content-box {
    margin: 0;
    padding: 0;

    .navbar-content-list {
      width: 100%;
      height: auto;
      list-style: none;
    }
    .navbar-link {
      height: .48rem;
      padding-left: .5rem;
      display: block;
      position: relative;
      font-size: .18rem;
      line-height: .48rem;
      text-align: left;
      color: $white;
    }
    .navbar-item-content {
      height: auto;
      width: 100%;
    }
    .navbar-icon {
      position: absolute;
      top: .17rem;
      left: .3rem;
      width: 0;
      height: 0;
      border-width: .05rem .05rem .05rem .05rem;
      border-style: solid;
      border-color: transparent transparent transparent $white;
    }
    .navbarActive {
      position: absolute;
      top: .2rem;
      left: .3rem;
      transform: rotate(90deg);
    }
    .navbar-item-link {
      display: block;
      padding-left: .7rem;
      font-size: .18rem;
      line-height: .48rem;
      text-align: left;
      color: #a4aab0;
      &.active,
      &:hover {
        background-color: #095481;
        color: $white;
      }
    }
  }
}
.ssssss {
  background-color: #333333;
  color: $white;
}
.not-display {
  display: block !important;
}
</style>