<template>
  <div class="title-wrapper" :class="{titleline: hasline}">
    <slot></slot>
    <div class="part-title" :class="{widthwarpper: ifWidth}">
      <div class="title-name">{{titleName}}</div>
    </div>
    <div class="line-after">
      <div class="under-line"></div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
export default {
  components: {},
  props: ['titleName', 'hasline', 'ifWidth'],
  name: 'partTitle',
  data() {
    return {};
  },
  methods: {
    ...mapActions([])
  },
  watch: {},
  computed: {
    ...mapGetters([])
  },
  created() { },
  mounted() { }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../assets/scss/common.scss";
.title-wrapper {
  position: relative;
  width: 100%;
  height: .4rem;
  padding-right: 1.07rem;
  &.titleline {
    .part-title {
      width: calc(100% - 1.07rem);
      border-top: .04rem solid $tit;
      .title-name {
        top: -.04rem;
        &::before {
          display: none;
        }
      }
    }
    .line-after {
      &::before {
        border-left-color: $tit;
      }
      .under-line {
        background-color: $tit;
        &::before {
          border-right-color: $tit;
        }
      }
    }
  }
  .part-title {
    position: absolute;
    left: 0;
    top: 0;
    width: 1.75rem;
    height: 100%;
    border-top: none;
    overflow: hidden;
    &.widthwarpper {
      width: 1.95rem;
    }
    .title-name {
      position: absolute;
      padding: 0 .18rem;
      left: 0;
      top: 0;
      color: $white;
      min-width: 1.2rem;
      font-size: .2rem;
      text-align: center;
      line-height: .4rem;
      background-color: $tit;
      &::after {
        position: absolute;
        right: -.6rem;
        bottom: 0;
        z-index: 2;
        border: .3rem solid transparent;
        border-top-width: .4rem;
        border-bottom-width: .4rem;
        border-left-color: $tit;
        content: "";
      }
      &::before {
        position: absolute;
        right: -.8rem;
        bottom: 0;
        z-index: 2;
        border: .3rem solid transparent;
        border-top-width: .4rem;
        border-bottom-width: .4rem;
        border-left-color: #012948;
        content: "";
      }
    }
  }
  .line-after {
    position: absolute;
    right: 0;
    top: 0;
    width: 1.07rem;
    height: .04rem;
    padding-left: .12rem;
    overflow: hidden;
    &::before {
      position: absolute;
      left: 0;
      bottom: 0;
      border: .03rem solid transparent;
      border-top-width: .04rem;
      border-bottom-width: .04rem;
      border-left-color: transparent;
      content: "";
    }
    .under-line {
      position: relative;
      width: 100%;
      height: .04rem;
      background-color: transparent;
      &::before {
        position: absolute;
        left: -.06rem;
        top: 0;
        border: .03rem solid transparent;
        border-top-width: .04rem;
        border-bottom-width: .04rem;
        border-right-color: transparent;
        content: "";
      }
    }
  }
}
</style>