<script>
import { mapGetters, mapActions } from "vuex";
import partTitle from '@/components/part-title.vue';

export default {
  components: {
    partTitle
  },
  name: "flowChart",
  props: ["alarmDetail"],
  data() {
    return {
      arrowLight: {
        smart: false,
        police: false,
        finish: false
      },
      arrowTime: null,
      areaName: '',
      placeName: ''
    }
  },
  methods: {
    ...mapActions(["setModal",
      "setPageNum",
      "setQueryAlarmStartDate",
      "setQueryAlarmEndDate",
      "setQueryAlarmStatus",
      "setQueryAlarmResult",
      "setQueryAlarmType",
      "setQueryUserId",
      "setSelectType",
      "setOverviewData",
      "clearAlarm",
      "setMinTitle",
      "clearCauseData"
      ]),

    clickList(type){
      this.setMinTitle(type);
      this.clearAlarm();
      this.clearCauseData();
      this.setPageNum("clear");
      this.setModal(true);
      this.$router.push({ path: "/caseListNum" });
    }
  },
  watch: {
  },
  computed: {
    ...mapGetters(['tempNum'])
  },
  created() {
  },
  mounted() {
    this.arrowLight.smart = true;
    this.arrowLight.police = true;
    this.arrowLight.finish = true;
    this.arrowTime = setTimeout(() => {
      this.arrowLight.smart = false;
      this.arrowLight.police = false;
      this.arrowLight.finish = false;
    }, 3000);
  }
};
</script>

