<template>
  <div class="paper-wrapper">
    <modal-header :titleName="titleName"></modal-header>
     <div class="case-content">
      <el-scrollbar class="scroll-deatil full-content">


        <img class="paper-content" v-for="(item,index) in caseFiles" :key="index" :src="'data:image/jpeg;base64,'+item" alt="">
      </el-scrollbar>
    </div>
  </div>
</template>
<script>
import { mapGetters, mapActions } from "vuex";
import modalHeader from "@/components/modal-header.vue";
import apiService from "../../api/index";

export default {
  components: {
    modalHeader
  },
  name: "paper",
  data() {
    return {
      caseFiles: [],
      titleName: "协议书预览"
    };
  },
  methods: {
    ...mapActions([])
  },
  watch: {},
  computed: {
    ...mapGetters(["outerCaseInfo"])
  },
  created() {
    let that = this;
    apiService
      .previewDocument({
        caseId: this.outerCaseInfo.properties.$id
      })
      .then(result => {
        const data = result.data;
        if (data.success) {
          that.caseFiles = data.pics;
        }
        // console.log('previewDocument', result);
      });
  },
  mounted() {}
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../../assets/scss/common.scss";
.paper-wrapper {
  position: absolute;
  top: 1rem;
  left: 0;
  right: 0;
  margin: auto;
  z-index: 3;
  width: 40%;
  font-size: .16rem;
  height: calc(100% - 2rem);
  background-color: #002543;
  .case-content {
    width: 100%;
    height: calc(100% - .64rem);
    padding-top: .2rem;
    background-color: #00060b;
  }
  .scroll-deatil {
    width: 100%;
    height: 100%;
  }
  .case-detail-title {
    width: 100%;
    height: auto;
    padding-left: .3rem;
    padding-right: .3rem;
    color: $white;
    .number-wrap {
      height: .7rem;
      line-height: .7rem;
      font-size: .28rem;
      .iconfont {
        margin-right: .15rem;
        font-size: .28rem;
      }
    }
    .iconsuccess {
      padding-left: .09rem;
      font-size: .24rem;
    }
    .type-wrap {
      height: .7rem;
      line-height: .7rem;
      font-size: .2rem;
      font-weight: inherit;
      span {
        position: relative;
        display: block;
        &::after {
          position: absolute;
          top: 0;
          bottom: 0;
          left: -.15rem;
          width: .08rem;
          height: .08rem;
          margin: auto;
          border-radius: 50%;
          content: "";
          background: #18dd7b;
        }
        &.bureaus::after {
          background-color: #e83d12;
        }
        &.office::after {
          background-color: #ede73b;
        }
        &.committee::after {
          background-color: $blue;
        }
        &.end::after {
          background-color: $green;
        }
        &.receive::after {
          background-color: #743eff;
        }
        &.ready::after {
          background-color: #18dd7b;
        }
      }
      .ready {
        cursor: pointer;
        animation: mymove 1s 3;
        -webkit-animation: mymove 1s 3; /*Safari and Chrome*/
      }
    }
  }
  .paper-content {
    width: 100%;
    display: block;
    margin: 0 auto;
  }
}
</style>