<template>
  <div class="left-wrapper">
    <div class="left-main">
      <part-title titleName="概览" class="tab-one">
        <div class="progress-line-box first-progress">
          <div class="progress-line progress-line-first">
            <div :style="{'width': countArr[0].pre}" class="progress-current-width" v-if="countArr.length > 0">
              <div class="progress-inner-current-width" :class="{newData: newData.count}"></div>
            </div>
          </div>
          <div class="progress-line progress-line-second">
            <div :style="{'width': countArr[1].pre}" class="progress-current-width" v-if="countArr.length > 0">
              <div class="progress-inner-current-width" :class="{newData: newData.count}"></div>
            </div>
          </div>
          <div class="progress-line progress-line-third">
            <div :style="{'width': countArr[2].pre}" class="progress-current-width" v-if="countArr.length > 0">
              <div class="progress-inner-current-width" :class="{newData: newData.count}"></div>
            </div>
          </div>
          <div class="progress-line progress-line-forth">
            <div :style="{'width': countArr[3].pre}" class="progress-current-width" v-if="countArr.length > 0">
              <div class="progress-inner-current-width" :class="{newData: newData.count}"></div>
            </div>
          </div>
          <div class="mask-disc" v-if="countArr.length > 0">
            <div>{{countArr[0].name}}: {{countArr[0].num}}</div>
            <div>{{countArr[1].name}}: {{countArr[1].num}}</div>
            <div>{{countArr[2].name}}: {{countArr[2].num}}</div>
            <div>{{countArr[3].name}}: {{countArr[3].num}}</div>
          </div>
        </div>
      </part-title>
      <ul class="count-box clearfix">
        <li class="count-list pull-left">
          <div class="count-title">
            <div class="count-title-content clearfix" @click="caseList('totalCount')">
              <div class="title-main pull-left">纠纷总量</div>
              <div class="title-num pull-left">{{overviewData.totalCount}}</div>
            </div>
          </div>
          <div class="count-coutent">
            <div class="count-content-content clearfix">
              <div class="year-content pull-left" @click="caseList('dayCount')">
                <div class="year-main">今日纠纷</div>
                <div class="yaer-num">{{overviewData.dayCount}}</div>
              </div>
              <div class="year-content pull-left" @click="caseList('yearCount')">
                <div class="year-main">今年纠纷</div>
                <div class="day-num">{{overviewData.yearCount}}</div>
              </div>
            </div>
          </div>
        </li>
        <li class="count-list pull-left">
          <div class="count-title">
            <div class="count-title-content clearfix" @click="caseList('totalHandledCount')">
              <div class="title-main pull-left">已处置总量</div>
              <div class="title-num pull-left">{{overviewData.totalHandledCount}}</div>
            </div>
          </div>
          <div class="count-coutent">
            <div class="count-content-content clearfix">
              <div class="year-content pull-left" @click="caseList('dayHandledCount')">
                <div class="year-main">今日处置</div>
                <div class="yaer-num">{{overviewData.dayHandledCount}}</div>
              </div>
              <div class="year-content pull-left" @click="caseList('yearHandledCount')">
                <div class="year-main">今年处置</div>
                <div class="day-num">{{overviewData.yearHandledCount}}</div>
              </div>
            </div>
          </div>
        </li>
        <li class="count-list pull-left">
          <div class="count-title">
            <div class="count-title-content clearfix" @click="caseList('totalFinishCount')">
              <div class="title-main pull-left">调解成功</div>
              <div class="title-num pull-left">{{overviewData.totalFinishCount}}</div>
            </div>
          </div>
          <div class="count-coutent">
            <div class="count-content-content clearfix">
              <div class="year-content pull-left" @click="caseList('dayFinishCount')">
                <div class="year-main">今日达成</div>
                <div class="yaer-num">{{overviewData.dayFinishCount}}</div>
              </div>
              <div class="year-content pull-left" @click="caseList('yearFinishCount')">
                <div class="year-main">今年达成</div>
                <div class="day-num">{{overviewData.yearFinishCount}}</div>
              </div>
            </div>
          </div>
        </li>
        <li class="count-list pull-left">
          <div class="count-title">
            <div class="count-title-content clearfix" @click="caseList('totalTdCount')">
              <div class="title-main pull-left">劝说平息</div>
              <div class="title-num pull-left">{{overviewData.totalPersuadeCount}}</div>
            </div>
          </div>
          <div class="count-coutent">
            <div class="count-content-content clearfix">
              <div class="year-content pull-left" @click="caseList('dayPersuadeCount')">
                <div class="year-main">今日平息</div>
                <div class="yaer-num">{{overviewData.dayPersuadeCount}}</div>
              </div>
              <div class="year-content pull-left" @click="caseList('yearPersuadeCount')">
                <div class="year-main">今年平息</div>
                <div class="day-num">{{overviewData.yearPersuadeCount}}</div>
              </div>
            </div>
          </div>
        </li>
      </ul>
    </div>
    <div class="left-main">
      <part-title titleName="纠纷类型" class="tab-two">
        <div class="progress-line-box">
          <div class="progress-line progress-line-first" v-if="rankArr.length > 0">
            <div :style="{'width': rankArr[0].pre}" class="progress-current-width">
              <div class="progress-inner-current-width" :class="{newData: newData.rank}"></div>
            </div>
          </div>
          <div class="progress-line progress-line-second" v-if="rankArr.length > 0">
            <div :style="{'width': rankArr[1].pre}" class="progress-current-width">
              <div class="progress-inner-current-width" :class="{newData: newData.rank}"></div>
            </div>
          </div>
          <div class="progress-line progress-line-third" v-if="rankArr.length > 0">
            <div :style="{'width': rankArr[2].pre}" class="progress-current-width">
              <div class="progress-inner-current-width" :class="{newData: newData.rank}"></div>
            </div>
          </div>
          <div class="progress-line progress-line-forth" v-if="rankArr.length > 0">
            <div :style="{'width': rankArr[3].pre}" class="progress-current-width">
              <div class="progress-inner-current-width" :class="{newData: newData.rank}"></div>
            </div>
          </div>
          <div class="mask-disc" v-if="rankArr.length > 0">
            <div>{{rankArr[0].name}}: {{rankArr[0].num}}</div>
            <div>{{rankArr[1].name}}: {{rankArr[1].num}}</div>
            <div>{{rankArr[2].name}}: {{rankArr[2].num}}</div>
            <div>{{rankArr[3].name}}: {{rankArr[3].num}}</div>
          </div>
        </div>
      </part-title>
      <div class="pie-content clearfix">
        <div class="pie-main pull-left">
          <div class="pie-title pull-left">总占比：</div>
          <div class="pie-box pull-left">
            <echart-type :dataList="typeTotalRank" chartId="pie-one"></echart-type>
          </div>
        </div>
        <div class="pie-main pull-left">
          <div class="pie-title pull-left">年占比：</div>
          <div class="pie-box pull-left">
            <echart-type :dataList="typeYearRank" chartId="pie-two"></echart-type>
          </div>
        </div>
        <div class="pie-main pull-left">
          <div class="pie-title pull-left">日占比：</div>
          <div class="pie-box pull-left">
            <echart-type :dataList="typeDayRank" chartId="pie-three"></echart-type>
          </div>
        </div>
        <div class="pie-main pull-left">
          <div class="pie-detail pull-left" @click="showlist(item.name)" v-for="(item,index) in rankArr" :key="index">
            <div class="pie-block pull-left"></div>{{item.name}}
          </div>
        </div>
      </div>
    </div>
    <div class="left-main">
      <part-title titleName="区排行" class="tab-three" :hasline="true"></part-title>
      <div class="bar-content">
        <!-- 年月tab切换 -->
        <div class="bar-detail clearfix">
          <div
            class="bar-year-detail pull-left"
            :class="{'activeYear': activeStatus}"
            @click="chanageData('Year')"
          >年</div>
          <div
            class="bar-year-detail pull-left"
            :class="{'activeYear': !activeStatus}"
            @click="chanageData('Day')"
          >日</div>
          <!-- <div class="detail-day pull-right">
            <div class="detail-block-other pull-left"></div>年
          </div>
          <div class="detail-year pull-right">
            <div class="detail-block pull-left"></div>日
          </div>-->
        </div>
        <div class="bar-wrapper" :class="{'activeScorll': activeStatus}" v-show="activeStatus">
          <div class="bar-border clearfix">
            <div class="border-content pull-left" v-for="(item,index) in borderList" :key="index"></div>
          </div>
          <div class="scoll-box" @mouseenter="removeBarlst()" @mouseleave="initList()">
            <el-scrollbar class="sider-scroll">
              <div class="bar-list clearfix" v-for="(item,index) in barList" :key="index">
                <div class="bar-name pull-left">{{item.rank}}.{{item.name}}</div>
                <div class="bar-length-content pull-left">
                  <div class="bar-length pull-left" :style="'width:'+item.precent+';'"></div>
                  <div class="bar-value pull-left" :style="'left:'+item.precent+';'">{{item.count}}</div>
                </div>
                <!-- <div class="bar-count pull-right">({{item.count}})</div> -->
              </div>
            </el-scrollbar>
          </div>
          <div class="scoll-line"></div>
        </div>
        <div class="bar-wrapper" :class="{'activeScorll': !activeStatus}" v-show="!activeStatus">
          <div class="bar-border clearfix">
            <div class="border-content pull-left" v-for="(item,index) in borderList" :key="index"></div>
          </div>
          <div class="scoll-box" @mouseenter="removeBarlst()" @mouseleave="initList()">
            <el-scrollbar class="sider-scroll">
              <div class="bar-list clearfix" v-for="(item,index) in barList" :key="index">
                <div class="bar-name pull-left">{{item.rank}}.{{item.name}}</div>
                <div class="bar-length-content pull-left">
                  <div class="bar-length pull-left" :style="'width:'+item.precentData+';'"></div>
                  <div
                    class="bar-value pull-left"
                    :style="'left:'+item.precentData+';'"
                  >{{item.value}}</div>
                </div>
                <!-- <div class="bar-count pull-right">({{item.count}})</div> -->
              </div>
            </el-scrollbar>
          </div>
          <div class="scoll-line"></div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import pieEChart from "@/components/pie-echart.vue";
import barEChart from "@/components/bar-echart.vue";
import echartType from "@/components/echart-type.vue";
import partTitle from "@/components/part-title.vue";
import apiService from "../api/index.js";
import util from "../util/index.js";

export default {
  components: {
    pieEChart,
    barEChart,
    echartType,
    partTitle
  },
  name: "left",
  data() {
    return {
      overviewData: {},
      typeTotalRank: {},
      typeYearRank: {},
      typeDayRank: {},
      barList: [],
      realBarlist: [], //最原始的barlist值
      barData: [
        // { name: "徐汇区", value: 124 },
        // { name: "黄浦区", value: 134 },
        // { name: "崇明区", value: 24 },
        // { name: "杨浦区", value: 144 },
        // { name: "长宁区", value: 74 }
      ],
      borderList: [
        { id: 0 },
        { id: 0 },
        { id: 0 },
        { id: 0 },
        { id: 0 },
        { id: 0 },
        { id: 0 }
      ],
      activeStatus: true,
      countArr: [],
      rankArr: [],
      newData: {
        count: false,
        rank: false
      },
      countDataTime: null,
      rankDataTime: null,
      isScroll: true
    };
  },
  methods: {
    ...mapActions([
      "setModal",
      "setPageNum",
      "setQueryAlarmStartDate",
      "setQueryAlarmEndDate",
      "setQueryAlarmStatus",
      "setQueryAlarmResult",
      "setQueryAlarmType",
      "setQueryUserId",
      "setSelectType",
      "setOverviewData",
      "clearAlarm",
      "setMinTitle",
      "setCauseData",
      "setCauseType",
      "clearCauseData",
      "clearCauseType",
      'changeIndexDataArr'
    ]),
    getPrecent() {
      for (let i = 0; i < this.barList.length - 1; i++) {
        for (let j = 0; j < this.barList.length - i - 1; j++) {
          if (this.barList[j].count < this.barList[j + 1].count) {
            let temp = this.barList[j];
            this.barList[j] = this.barList[j + 1];
            this.barList[j + 1] = temp;
          }
        }
      }
      // return this.barList;
      // console.log(this.barList);
      //设置年百分比
      var maxNum = Math.ceil(this.barList[0].count / 10) * 10;
      for (var i = 0; i < this.barList.length; i++) {
        this.barList[i]["precent"] = this.barList[i].count / maxNum * 100 + "%";
      }
      // this.barList.forEach((e, i) => {
      //   e.rank = i + 1;
      // });
      //设置日百分比
      var maxNumData = Math.ceil(this.barList[0].value / 10) * 10;
      for (var i = 0; i < this.barList.length; i++) {
        this.barList[i]["precentData"] = this.barList[i].value / maxNumData * 100 + "%";
      }
      this.realBarlist = this.barList;
      this.barList.forEach((e, i) => {
        e.rank = i + 1;
      });
      if(this.isScroll) {
        this.addBarList();
      }
    },
    // barList增加四个item。实现无缝轮播
    addBarList() {
      let list = this.barList;
      let otherList = list.slice(0, 5);
      this.barList = list.concat(otherList);
    },
    initList() {
      this.isScroll = true;
      this.addBarList();
    },
    // barList减少四个item。实现无缝轮播
    removeBarlst() {
      this.isScroll = false
      this.barList = this.realBarlist;
    },
    setCountArr() {
      this.countArr = [];
      this.countArr.push({
        pre: "100%",
        name: "纠纷总量",
        num: this.overviewData.totalCount
      });
      this.countArr.push({
        pre:
          Math.ceil(
            this.overviewData.totalHandledCount / this.overviewData.totalCount * 100
          ) + "%",
        name: "已处置总量",
        num: this.overviewData.totalHandledCount
      });
      this.countArr.push({
        pre:
          Math.ceil(
            this.overviewData.totalFinishCount / this.overviewData.totalCount * 100
          ) + "%",
        name: "调解成功",
        num: this.overviewData.totalFinishCount
      });
      this.countArr.push({
        pre:
          Math.ceil(
            this.overviewData.totalPersuadeCount / this.overviewData.totalCount * 100
          ) + "%",
        name: "劝说平息",
        num: this.overviewData.totalPersuadeCount
      });
      this.countArr = this.countArr.concat([]);

      // console.log("this.countArr:", this.countArr);
    },
    setRankArr() {
      this.rankArr = [];
      // console.log('this.typeTotalRank', this.typeTotalRank);
      this.typeTotalRank.list.forEach(item => {
        this.rankArr.push({
          pre: Math.ceil(item.value / this.typeTotalRank.totalCount * 100) + '%',
          name: item.name,
          num: item.value
        });
      });
      this.rankArr = this.rankArr.concat([]);

      // console.log('this.rankArr', this.rankArr);
    },
    getBarList(orgId) {
      let that = this;
      let params = { "orgId": orgId || this.searchByOrgId };
      apiService.getLeftDate(params).then(result => {
        let data = result.data;
        that.barList = data.zoneRank;
        that.typeTotalRank = data.typeTotalRank;
        that.typeYearRank = data.typeYearRank;
        that.typeDayRank = data.typeDayRank;
        that.setOverviewData(data.overviewData);
        that.overviewData = data.overviewData;
        that.personData = data.personData;

        that.getPrecent(that.typeTotalRank);

        that.setCountArr();
        that.setRankArr();
        that.changeIndexDataArr('add');
      });
    },
    caseList(type) {
      /*** 
        startDate: state.queryAlarmStartDate,
        endDate: state.queryAlarmEndDate,
        status: state.queryAlarmStatus,
        result: state.queryAlarmResult,
        type: state.queryAlarmType,
        userId: state.queryUserId
        以上参数对应修改方法：
        this.setQueryAlarmStartDate(参数)
        this.setQueryAlarmEndDate(参数)
        this.setQueryAlarmStatus(参数)
        this.setQueryAlarmResult(参数)
        this.setQueryAlarmType(参数)
        this.setQueryUserId(参数)
      */

      /***** 
        typeId: state.outerCurrentTypeId,
        id: state.outerCurrentId,
        以上参数对应修改方法
        this.setSelectType({ type: '' || 'area' || 'place',  val: '(传入id)' || ''});
      */
      this.clearAlarm();
      switch (type) {
        case "totalCount":
          this.setQueryAlarmStartDate("");
          this.setQueryAlarmEndDate("");
          this.setQueryAlarmStatus("");
          this.setQueryAlarmResult("");
          this.setQueryAlarmType("");
          this.setQueryUserId("");
          this.setMinTitle("纠纷总量");
          this.clearCauseData();
          break;
        case "totalHandledCount":
          this.setQueryAlarmStartDate("");
          this.setQueryAlarmEndDate("");
          this.setQueryAlarmStatus("FINISHED");
          this.setQueryAlarmResult("");
          this.setQueryAlarmType("");
          this.setQueryUserId("");
          this.setMinTitle("已处置总量");
          this.clearCauseData();
          break;
        case "totalFinishCount":
          this.setQueryAlarmStartDate("");
          this.setQueryAlarmEndDate("");
          this.setQueryAlarmStatus("");
          this.setQueryAlarmResult("f4d14631f79446af80090cd7b87059f3,24093700cb6d406a8e5e707986d29ee2");
          this.setQueryAlarmType("");
          this.setQueryUserId("");
          this.setMinTitle("成功调解总量");
          this.clearCauseData();
          break;
        case "totalTdCount":
          this.setQueryAlarmStartDate("");
          this.setQueryAlarmEndDate("");
          this.setQueryAlarmStatus("");
          this.setQueryAlarmResult("24093700cb6d406a8e5e707986d29ee2");
          this.setQueryAlarmType("");
          this.setQueryUserId("");
          this.setMinTitle("劝说平息");
          this.clearCauseData();
          break;
        case "yearCount":
          this.setQueryAlarmStartDate(
            util.getFirstDayOfYear(new Date(), "yyyy-MM-dd")
          );
          this.setQueryAlarmEndDate(util.format(new Date(), "yyyy-MM-dd"));
          this.setQueryAlarmStatus("");
          this.setQueryAlarmResult("");
          this.setQueryAlarmType("");
          this.setQueryUserId("");
          this.setMinTitle("今年纠纷");
          break;
        case "yearHandledCount":
          this.setQueryAlarmStartDate(
            util.getFirstDayOfYear(new Date(), "yyyy-MM-dd")
          );
          this.setQueryAlarmEndDate(util.format(new Date(), "yyyy-MM-dd"));
          this.setQueryAlarmStatus("FINISHED");
          this.setQueryAlarmResult("");
          this.setQueryAlarmType("");
          this.setQueryUserId("");
          this.setMinTitle("今年处置");
          break;
        case "yearFinishCount":
          this.setQueryAlarmStartDate(
            util.getFirstDayOfYear(new Date(), "yyyy-MM-dd")
          );
          this.setQueryAlarmEndDate(util.format(new Date(), "yyyy-MM-dd"));
          this.setQueryAlarmStatus("");
          this.setQueryAlarmResult("f4d14631f79446af80090cd7b87059f3,24093700cb6d406a8e5e707986d29ee2");
          this.setQueryAlarmType("");
          this.setQueryUserId("");
          this.setMinTitle("今年调解");
          break;
        case "yearTdCount":
          this.setQueryAlarmStartDate(
            util.getFirstDayOfYear(new Date(), "yyyy-MM-dd")
          );
          this.setQueryAlarmEndDate(util.format(new Date(), "yyyy-MM-dd"));
          this.setQueryAlarmStatus("");
          this.setQueryAlarmResult("c8dc62ce68044b5abe4ecad7d6a4c71e");
          this.setQueryAlarmType("");
          this.setQueryUserId("");
          this.setMinTitle("今年退单");
          break;
        case "dayCount":
          this.setQueryAlarmStartDate(util.format(new Date(), "yyyy-MM-dd"));
          this.setQueryAlarmEndDate(util.format(new Date(), "yyyy-MM-dd"));
          this.setQueryAlarmStatus("");
          this.setQueryAlarmResult("");
          this.setQueryAlarmType("");
          this.setQueryUserId("");
          this.setMinTitle("今日纠纷");
          break;
        case "dayHandledCount":
          this.setQueryAlarmStartDate(util.format(new Date(), "yyyy-MM-dd"));
          this.setQueryAlarmEndDate(util.format(new Date(), "yyyy-MM-dd"));
          this.setQueryAlarmStatus("FINISHED");
          this.setQueryAlarmResult("");
          this.setQueryAlarmType("");
          this.setQueryUserId("");
          this.setMinTitle("今日处置");
          break;
        case "dayFinishCount":
          this.setQueryAlarmStartDate(util.format(new Date(), "yyyy-MM-dd"));
          this.setQueryAlarmEndDate(util.format(new Date(), "yyyy-MM-dd"));
          this.setQueryAlarmStatus("");
          this.setQueryAlarmResult("f4d14631f79446af80090cd7b87059f3,24093700cb6d406a8e5e707986d29ee2");
          this.setQueryAlarmType("");
          this.setQueryUserId("");
          this.setMinTitle("今日调解");
          break;
        case "dayTdCount":
          this.setQueryAlarmStartDate(util.format(new Date(), "yyyy-MM-dd"));
          this.setQueryAlarmEndDate(util.format(new Date(), "yyyy-MM-dd"));
          this.setQueryAlarmStatus("");
          this.setQueryAlarmResult("c8dc62ce68044b5abe4ecad7d6a4c71e");
          this.setQueryAlarmType("");
          this.setQueryUserId("");
          this.setMinTitle("今日退单");
          break;
          case "dayPersuadeCount":
          this.setQueryAlarmStartDate(util.format(new Date(), "yyyy-MM-dd"));
          this.setQueryAlarmEndDate(util.format(new Date(), "yyyy-MM-dd"));
          this.setQueryAlarmStatus("");
          this.setQueryAlarmResult("24093700cb6d406a8e5e707986d29ee2");
          this.setQueryAlarmType("");
          this.setQueryUserId("");
          this.setMinTitle("今日平息");
          break;
          case "yearPersuadeCount":
          this.setQueryAlarmStartDate(util.getFirstDayOfYear(new Date(), "yyyy-MM-dd"));
          this.setQueryAlarmEndDate(util.format(new Date(), "yyyy-MM-dd"));
          this.setQueryAlarmStatus("");
          this.setQueryAlarmResult("24093700cb6d406a8e5e707986d29ee2");
          this.setQueryAlarmType("");
          this.setQueryUserId("");
          this.setMinTitle("今年平息");
          break;
      }
      this.setPageNum("clear");
      this.setModal(true);
      this.$router.push({ path: "/caseList" });
    },
    setNewDataCount() {
      clearTimeout(this.countDataTime);
      this.newData.count = true;
      this.countDataTime = setTimeout(() => {
        this.newData.count = false;
      }, 2000);
    },
    setNewDataRank() {
      clearTimeout(this.rankDataTime);
      this.newData.rank = true;
      this.rankDataTime = setTimeout(() => {
        this.newData.rank = false;
      }, 2000);
    },
    showlist(name) {
      this.clearAlarm();
      this.clearCauseData();
      this.clearCauseType();
      this.setQueryAlarmStartDate("");
      this.setQueryAlarmEndDate("");
      this.setQueryAlarmStatus("");
      this.setQueryAlarmResult("");
      this.setQueryAlarmType("");
      this.setQueryUserId("");
     
      this.setPageNum("clear");
      this.setModal(true);
      switch (name) {
           case "邻里纠纷":
           this.setMinTitle('邻里纠纷');
           this.setCauseData(["f7f2d5b19e26474ab6d4c80c68a20c32"]);
          break;
           case "婚恋纠纷":
            this.setMinTitle('婚恋纠纷');
            this.setCauseData(["e95fceb4d12040679298829d4774f702"]);
          break;
           case "医患纠纷":
           this.setMinTitle('医患纠纷');
           this.setCauseData(["046f958df66a46ff99226cd7371454a9"]);
          break;
           case "治安纠纷":
           this.setMinTitle('治安纠纷');
           this.setCauseType("NotIn");
           let val = ["f7f2d5b19e26474ab6d4c80c68a20c32", "e95fceb4d12040679298829d4774f702", "046f958df66a46ff99226cd7371454a9"];
           this.setCauseData(val);
          break;
      }
      this.$router.push({ path: "/caseList" });
    },
    chanageData(typeVal) {
      if (typeVal === 'Day') {
        this.activeStatus = false;
      } else if (typeVal === 'Year') {
        this.activeStatus = true;
      }
      // let params = {"orgId":this.searchByOrgId,"activeStatus" : this.activeStatus};
      // apiService.getLeftDate(params).then(result => {
      //   let data = result.data;
      //   this.barList = data.zoneRank;
      // });
    }
  },
  watch: {
    searchByOrgId: function () {
      // console.log("ssssss", this.searchByOrgId);
      this.getBarList(this.searchByOrgId);
    }
  },
  computed: {
    ...mapGetters(['searchByOrgId'])
  },
  created() { },
  mounted() {
    this.setNewDataCount();
    this.setNewDataRank();

    this.getBarList();
    setInterval(() => {
      this.getBarList();
    }, 10000);
  }
};
</script>

