<template>
  <div class="pie-wrap">
    <div :id="chartId"></div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import pie from 'echarts'

export default {
  components: {
  },
  props: ['chartId', 'chartData'],
  name: 'pie-echart',
  data() {
    return {
      data: [],
      totalCount: 0,
      chart: null
    }
  },
  methods: {
    ...mapActions([]),
    setChart() {
      this.chart.setOption({
        tooltip: false,
        legend: {
          x: 'center',
          y: 'bottom',
          textStyle: {
            color: '#fff'
          }
        },
        toolbox: false,
        calculable: true,
        series: [
          {
            type: 'pie',
            radius: '30%',
            center: ['50%', '50%'],
            roseType: 'radius',
            label: {
              color: '#fff',
              formatter: (data) => {
                let precent = data.value / this.totalCount;
                precent = precent.toFixed(2);
                return data.name + ': ' + precent + '%';
              }
            },
            lableLine: {
              normal: {
                show: false
              },
              emphasis: {
                show: true
              }
            },
            data: this.data
          }
        ],
        color: ['#004aff', '#b60000', '#049589', '#6e8923', '#eedf55', '#eea755']
      }, true);
    }
  },
  watch: {
    chartData(newVal) {
      this.data = newVal.list;
      this.totalCount = newVal.totalCount;
      // 异步加载数据
      // 填入数据
      this.setChart();
    }
  },
  computed: {
    ...mapGetters([
    ])
  },
  created() {
  },
  mounted() {
    this.data = this.chartData.list;
    this.totalCount = this.chartData.totalCount;
    this.chart = pie.init(document.getElementById(this.chartId));
    // 显示标题，图例和空的坐标轴
    this.setChart();
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../assets/scss/common.scss";
.pie-wrap {
  width: 100%;
  height: 100%;
  #pie-demo {
    width: 100%;
    height: 100%;
  }
}
</style>