<script>
import { mapGetters, mapActions } from "vuex";
import modalHeader from "@/components/modal-header.vue";
import api from "@/api/index.js";
// import radarEchart from '@/components/radar.vue';
import echartType from "@/components/echart-type.vue";

export default {
  components: {
    modalHeader,
    // radarEchart,
    echartType
  },
  name: "mediatorDetail",
  data() {
    return {
      titleName: "调解员",
      // radarperson: [
      //   {
      //     value: [80, 60, 70, 90, 50],
      //     name: '能力评析'
      //   }
      // ]
      personAbility: {
        totalCount: 100,
        list: [
          {
            name: "接受调解(劝说平息)",
            value: 20
          },
          {
            name: "调解不成",
            value: 20
          },
          {
            name: "无法联系",
            value: 20
          },
          {
            name: "拒绝调解",
            value: 20
          },
          {
            name: "非纠纷类退单",
            value: 20
          }
        ]
      }
    };
  },
  methods: {
    ...mapActions([
      "setmodalHasBack",
      "setPageNum",
      "setQueryAlarmStartDate",
      "setQueryAlarmEndDate",
      "setQueryAlarmStatus",
      "setQueryAlarmResult",
      "setQueryAlarmKey",
      "setQueryAlarmType",
      "setQueryUserId",
      "clearAlarm",
      "setIsIndex",
      "AgetMediatorDetailInfo",
      'setClearPerson'
    ]),
    fileUrl(id) {
      if (id) {
        return api.fileUrl + id + "/view?thumb=200x200";
      }
      return "./images/portrait.jpg";
    },
    showStaffCaseList(type, id) {
      this.clearAlarm();
      this.setmodalHasBack(true);
      this.setPageNum("clear");
      this.setQueryAlarmStartDate("");
      this.setQueryAlarmEndDate("");
      this.setQueryAlarmStatus("");
      this.setQueryAlarmResult("");
      this.setQueryAlarmKey("");
      this.setQueryAlarmType(type);
      this.setQueryUserId(id);
      this.$router.push({ path: "/caseList" });
    },
    contact(){
      api.contact();
    },

    setData1(id) {
      // console.log("outerMediatorDetail:", this.outerMediatorDetail);
      this.AgetMediatorDetailInfo(id);
      setTimeout(() => {
      if (!this.outerMediatorDetail.handleData) {
          return;
      }
      const totalCount = this.outerMediatorDetail.handleData.totalCount;
      const jieshoutiaojie = this.outerMediatorDetail.handleData.jieshoutiaojie;
      const tiaojiebucheng = this.outerMediatorDetail.handleData.tiaojiebucheng;
      const wufalianxi = this.outerMediatorDetail.handleData.wufalianxi;
      const jujuetiaojie = this.outerMediatorDetail.handleData.jujuetiaojie;
      const tuidan = this.outerMediatorDetail.handleData.tuidan;
      this.personAbility = {
        totalCount: totalCount,
        list: [
          {
            name: "接受调解(劝说平息)",
            value: jieshoutiaojie
          },
          {
            name: "调解不成",
            value: tiaojiebucheng
          },
          {
            name: "无法联系",
            value: wufalianxi
          },
          {
            name: "拒绝调解",
            value: jujuetiaojie
          },
          {
            name: "非纠纷类退单",
            value: tuidan
          }
        ]
      };
      }, 2000);
     
    },
    
  },
  watch: {},
  computed: {
    ...mapGetters([
      "outerMediatorDetail",
      "selfPersonType",
      "outerDispactcherDetail"
    ])
  },
  created() {
    setTimeout(() => {
      this.setIsIndex(false);
    }, 500);
    let that = this;
    if (this.selfPersonType === "mediator") {
      this.titleName = "调解员";
    } else {
      this.titleName = "调度员";
    }
    // this.setClearPerson();
  },
  mounted() {
    // this.setData1();
    // this.setData2();
  }
};
</script>

