<template>
  <div class="case-list">
    <modal-header :titleName="titleName"></modal-header>
    <div class="case-content clearfix">
      <!-- 左侧导航栏开始 -->
      <div class="case-content-left pull-left">
        <el-scrollbar class="navbar-content-scroll">
          <ul class="navbar-content-box">
            <li class="navbar-content-list" v-for="(item, index) in justiceBureausNew" :key="index">
              <a href="javascript:;" class="navbar-link" @click="changeNavbar(index)">
                <i
                  class="navbar-icon"
                  :class="{'navbarActive': navbarIndex === index}"
                  v-show="item.justiceOffices.length > 0"
                ></i>
                {{item["properties.name"]}}
              </a>
              <div class="navbar-item-content" v-show="navbarIndex === index">
                <a
                  href="javascript:;"
                  v-for="(justiceOffice, index) in item.justiceOffices"
                  :key="index"
                  class="navbar-item-link"
                  :class="[justiceOffice.type === 'active' ? 'active' : '']"
                  @click="chooseChild(justiceOffice)"
                >{{justiceOffice["properties.name"]}}</a>
              </div>
            </li>
          </ul>
        </el-scrollbar>
      </div>
      <!-- 左侧导航栏结束 -->
      <div class="case-content-right pull-left">
        <div class="pull-right list-count">总计：&nbsp;{{alarmsCount}}件</div>
        <div class="select-name-outer">
          <input
            class="select-name-inner"
            type="text"
            placeholder="请输入案件关键词搜索"
            v-model="searchBroadcast"
          >
          <div class="has-paper" :class="{'not-paper': chooseShowPaper}" @click="chooseSelectPaper"><span class="circle"><span class="right-icon"></span></span>是否已签署协议书</div>
          <div class="iconfont iconsousuo icon-select-btn"></div>
        </div>
        <div class="scroll-box">
          <div class="broadcas-title clearfix">
            <div class="pull-left broadcast-time">报警时间</div>
            <div class="pull-left broadcast-person">当事人</div>
            <div class="pull-left broadcast-disc">描述</div>
            <div class="pull-left broadcast-master">处置人员</div>
            <div class="pull-left broadcast-status">状态</div>
          </div>
          <el-scrollbar class="scroll-deatil" ref="broadcasbar">
            <div class="detail-content-box">
              <ul class="broadcas-list">
                <li
                  class="broadcas-list-item"
                  v-for="(item, index) in alarms"
                  :key="index"
                  @click="broadcastDetail(item.properties.$id)"
                >
                  <a class="broadcas-item clearfix" href="javascript:;">
                    <div class="pull-left broadcast-time">{{item.$displays.callTime}}</div>
                    <div class="pull-left broadcast-person">{{item.properties.callerName}}</div>
                    <div class="pull-left broadcast-disc">{{item.properties.desc}}</div>
                    <div class="pull-left broadcast-master">{{item.$displays.staff}}</div>
                    <div
                      class="pull-left broadcast-status wait"
                      v-if="item.properties.status==='ACCEPTED'"
                    >待分派</div>
                    <div
                      class="pull-left broadcast-status bureaus"
                      v-if="item.properties.status==='JB_DISPATCHED'"
                    >已派区局</div>
                    <div
                      class="pull-left broadcast-status office"
                      v-if="item.properties.status==='JO_DISPATCHED'||item.properties.status==='HZ_MC_DISPATCHED'"
                    >已派司法所/专调</div>
                    <div
                      class="pull-left broadcast-status committee"
                      v-if="item.properties.status==='MC_DISPATCHED'"
                    >已派调委会</div>
                    <div
                      class="pull-left broadcast-status end"
                      v-if="item.properties.status==='FINISHED'"
                    >已处置
                    <span class="ready iconfont iconsuccess" v-if="showPrint(item)"  @click="showPaper($event,item.caseInfo)"></span>
                    <!-- <span class="ready iconfont iconsuccess" @click="showPaper($event,item.caseInfo)"></span> -->
                    <!-- <span class="ready" v-if="showPrint(item.caseInfo)">
                      (达成协议) -->
                      <!-- <div class="tooltip-item pull-right">
                        <i class="iconfont iconsuccess"></i>
                        <div class="tool-tip">点击预览协议书</div>
                      </div> -->
                    <!-- </span> -->
                    </div>
                    <div
                      class="pull-left broadcast-status receive"
                      v-if="item.properties.status==='JO_ACCEPTED'"
                    >司法所已接收</div>

                    
                  </a>
                </li>
              </ul>
            </div>
          </el-scrollbar>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from "vuex";
import modalHeader from "@/components/modal-header.vue";

export default {
  components: {
    modalHeader
  },
  name: "caseList",
  data() {
    return {
      titleName: "实时警情播报",
      searchBroadcast: "",
      navbarIndex: -1,
      searchById: "",
      justiceBureausNew: [
        {
          type: "",
          id: "",
          justiceOffices: [
            {
              type: ""
            }
          ]
        }
      ],
      navbarLinkList: [
        { name: "宝山区司法局" },
        { name: "宝山区司法局" },
        { name: "宝山区司法局" }
      ],
      outerbureauData: [
        {
          id: 0,
          type: "active",
          name: "上海市司法局",
          children: []
        },
        {
          id: 1,
          type: "",
          name: "黄浦区司法局1",
          children: [
            {
              id: 11,
              type: "",
              name: "司法所1"
            },
            {
              id: 12,
              type: "",
              name: "司法所2"
            },
            {
              id: 13,
              type: "",
              name: "司法所3"
            },
            {
              id: 14,
              type: "",
              name: "司法所4"
            }
          ]
        }
      ],
      getListLoad: true,
      chooseShowPaper: false
    }
  },
  methods: {
    ...mapActions([
      "AgetAlarms",
      "setPageNum",
      "setmodalHasBack",
      "setCaseId",
      "clearAlarm",
      "setIsIndex",
      "clearCaseList",
      "changeOuterCase"
    ]),
    // 点击切换navbar的index
    changeNavbar(index) {
      if (this.navbarIndex === index) {
        this.navbarIndex = -1;
        return;
      } else {
        this.clearCaseList();
        this.setPageNum("clear");
        this.navbarIndex = index;
        this.navbarLinkList = this.justiceBureausNew[index].justiceOffices;
        this.justiceBureausNew.forEach(e => {
          e.type = "";
          if (!e.justiceOffices) {
            e.justiceOffices = [];
          }
          if (e.justiceOffices.length > 0) {
            e.justiceOffices.forEach(ele => {
              ele.type = "";
            });
          }
        });
        this.justiceBureausNew[index].type = "active";
        this.searchById = this.justiceBureausNew[index].id;
        this.getAlarms("", this.justiceBureausNew[index].id, this.chooseShowPaper);
      }
    },
    chooseChild(item) {
      this.clearCaseList();
      this.setPageNum("clear");
      this.justiceBureausNew.forEach(e => {
        e.type = "";
        if (!e.justiceOffices) {
          e.justiceOffices = [];
        }
        if (e.justiceOffices.length > 0) {
          e.justiceOffices.forEach(ele => {
            ele.type = "";
          });
        }
        if (e.justiceOffices === this.navbarLinkList) {
          e.type = "active";
          e.justiceOffices.forEach(ele => {
            ele.type = "";
            if (ele === item) {
              ele.type = "active";
            }
          });
        }
      });
      // 返回一个新数组，监听对象
      this.justiceBureausNew = this.justiceBureausNew.concat([]);
      this.searchById = item.id;
      this.getAlarms("", item.id,this.chooseShowPaper);
    },
    broadcastDetail(item) {
      this.setmodalHasBack(true);
      this.setCaseId(item).then(() => {
        this.$router.push({ path: "/caseDetail" });
      });
    },
    getAlarms(val, orgidVal, pdfId) {
      let that = this;
      let newval = val || "";
      let orgid = orgidVal || this.searchById;
      let params = { key: newval, orgId: orgid ,pdfId:pdfId};
      this.AgetAlarms(params).then(() => {
        this.$nextTick(() => {
          this.scrollbroadcastList = this.$refs["broadcasbar"].$refs["wrap"];
          let height =
            that.scrollbroadcastList.scrollHeight -
            that.scrollbroadcastList.scrollTop;
          if(height >= 656) {
            that.setPageNum('add');
            that.AgetAlarms(params);
          };
          this.scrollbroadcastList.onscroll = function () {
            if (
              that.scrollbroadcastList.scrollHeight -
                height -
                that.scrollbroadcastList.scrollTop <
              5
            ) {
              if (that.getListLoad) {
                that.getListLoad = false;
                that.setPageNum("add");
                that.AgetAlarms(params);
              }
            } else {
              that.getListLoad = true;
            }
          };
        });
      });
    },
    showPrint(alarmInfo) {
      if(!alarmInfo){
        return false;
      }
      if (alarmInfo.properties.pdfId) {
          return true;
      } else {
        return false;
      }
    },
    showPaper(e, caseInfo) {
      e.stopPropagation();
      this.setmodalHasBack(true);
      this.changeOuterCase(caseInfo);
      this.$router.push({ path: '/paperpage'});
    },
    chooseSelectPaper() {
      this.chooseShowPaper = !this.chooseShowPaper;
      // 筛选有协议书
      this.clearAlarm();
      this.setPageNum("clear");
      this.getAlarms('', this.searchById, this.chooseShowPaper);
    }
  },
  watch: {
    searchBroadcast(newVal) {
      this.clearAlarm();
      this.setPageNum("clear");
      this.getAlarms(newVal, this.searchById,this.chooseShowPaper);
    }
  },
  computed: {
    ...mapGetters([
      "alarms",
      "queryPageNum",
      "alarmsCount",
      "modalMinTitle",
      "justiceBureaus",
      "searchByOrgId"
    ])
  },
  created() {
    this.searchById = this.searchByOrgId;
    this.titleName = this.modalMinTitle || "警情播报";
    setTimeout(() => {
      this.setIsIndex(false);
    }, 500);
  },
  mounted() {
    this.clearCaseList(); // 每次进入页面清除掉之前的数据
    let that = this;
    this.getAlarms();
    let justiceBureausNew = this.justiceBureaus;
    this.justiceBureausNew = justiceBureausNew;
    // 选中带过来的司法局
    for (let val in justiceBureausNew) {
      if (justiceBureausNew[val].type === "active") {
        this.navbarIndex = parseInt(val);
        this.navbarLinkList = justiceBureausNew[val].justiceOffices;
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss" rel="stylesheet/scss">
@import "../../assets/scss/common.scss";
.case-list {
  position: absolute;
  top: 1.26rem;
  left: 10%;
  z-index: 3;
  width: 80%;
  font-size: .16rem;
  height: calc(100% - 2.52rem);
  background-color: #032b4a;
  .case-content {
    width: 100%;
    height: calc(100% - .64rem);
    .case-content-left {
      width: 18%;
      height: 100%;
      background-color: #01243f;
    }
    .case-content-right {
      width: 82%;
      height: 100%;
      padding: .24rem .12rem .3rem .3rem;
    }
  }
  .list-count {
    padding-top: .2rem;
    padding-right: .18rem;
    font-weight: lighter;
    font-size: .18rem;
    color: $white;
  }
  .select-name-outer {
    position: relative;
    width: 36%;
    height: .64rem;
    line-height: .36rem;
    padding-bottom: .16rem;
    z-index: 5;
    .has-paper {
      position: absolute;
      right: 0;
      top: 0;
      transform: translateX(100%);
      height: .48rem;
      line-height: .48rem;
      padding-left: .6rem;
      font-size: .18rem;
      color: #fff;
      cursor: pointer;
      user-select: none;
      .circle {
        position: absolute;
        left: .36rem;
        top: .15rem;
        width: .18rem;
        height: .18rem;
        // border-radius: 50%;
        border: .02rem solid #0a92d9;
        .right-icon {
          position: absolute;
          left: .04rem;
          top: -.03rem;
          width: .08rem;
          height: .15rem;
          transform: rotate(45deg);
          border: .02rem solid transparent;
        }
      }
      &.not-paper {
        .right-icon {
          border-bottom-color: #0a92d9;
          border-right-color: #0a92d9;
        }
      }
    }
    .select-name-inner {
      display: block;
      width: 100%;
      height: 100%;
      padding: 0 .35rem 0 .22rem;
      margin: 0;
      border: none;
      font-size: .16rem;
      font-weight: lighter;
      color: $white;
      background-color: #08406e;
      &::placeholder {
        color: #9abedb;
      }
      &:focus {
        outline: none;
      }
    }
    .icon-select-btn {
      position: absolute;
      right: 0;
      top: 0;
      padding-right: .18rem;
      line-height: .48rem;
      font-size: .22rem;
      color: #bcedff;
    }
  }
  .scroll-box {
    position: relative;
    width: 100%;
    height: calc(100% - .52rem);
  }
  .scroll-deatil {
    position: relative;
    width: 100%;
    height: 100%;
    &.full-content {
      height: 100%;
    }
    .detail-content-box {
      width: 100%;
      padding-right: .18rem;
    }
  }
  .detail-content-box {
    width: 100%;
    padding-right: .18rem;
  }
  .broadcas-list {
    width: 100%;
    margin: 0;
    padding: 0;
    color: $white;
    font-size: .16rem;
    text-align: left;
    padding-top: .46rem;
    .broadcas-list-item {
      position: relative;
      width: 100%;
      height: .61rem;
      border-bottom: .01rem solid #333b64;
      &:hover {
        background-color: #166f9a;
      }
    }
    .broadcas-item {
      position: absolute;
      left: 0;
      top: 0;
      display: block;
      width: 100%;
      height: .61rem;
      color: $white;
      line-height: .6rem;
      font-weight: normal;
      .broadcast-status {
        position: relative;
        padding-left: .36rem;
        &::before {
          position: absolute;
          left: .24rem;
          top: 50%;
          transform: translateY(-50%);
          width: .05rem;
          height: .05rem;
          border-radius: 50%;
          background-color: #f19149;
          content: "";
        }
        &.bureaus::before {
          background-color: #e83d12;
        }
        &.office::before {
          background-color: #ede73b;
        }
        &.committee::before {
          background-color: $blue;
        }
        &.end::before {
          background-color: $green;
        }
        &.receive::before {
          background-color: #743eff;
        }
      }
    }
  }
  .broadcas-title {
    position: absolute;
    left: 0;
    top: 0;
    z-index: 2;
    width: calc(100% - .18rem);
    height: .48rem;
    line-height: .48rem;
    color: $white;
    text-align: left;
    font-size: .16rem;
    font-weight: lighter;
    background-color: #1c6c9b;
  }
  .broadcast-num {
    width: 26%;
    min-height: .46rem;
    padding-left: .2rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .broadcast-time {
    width: 26%;
    min-height: .46rem;
    padding-left: .2rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-weight: lighter;
    font-size: .18rem;
  }
  .broadcast-person {
    width: 11%;
    min-height: .46rem;
    padding-left: .2rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-weight: lighter;
    font-size: .18rem;
    &.has-content {
      height: .61rem;
      &:hover {
        background-color: $brand;
      }
    }
  }
  .broadcast-disc {
    width: 33%;
    min-height: .46rem;
    padding-left: .2rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-weight: lighter;
    font-size: .18rem;
  }
  .broadcast-master {
    width: 12%;
    min-height: .46rem;
    padding-left: .2rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-weight: lighter;
    font-size: .18rem;
    &.content-master {
      height: .61rem;
      &:hover {
        background-color: $brand;
      }
    }
  }
  .broadcast-status {
    width: 18%;
    min-height: .46rem;
    padding-left: .2rem;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-weight: lighter;
    font-size: .18rem;
    z-index: 1;
  }
  // 左侧导航栏样式
  .navbar-main-title {
    padding-left: .3rem;
    font-size: .18rem;
    color: $white;
    line-height: .48rem;
    text-align: left;
  }
  .navbar-content-scroll {
    height: 100%;
  }
  .navbar-content-box {
    margin: 0;
    padding: 0;

    .navbar-content-list {
      width: 100%;
      height: auto;
      list-style: none;
    }
    .navbar-link {
      height: .48rem;
      padding-left: .5rem;
      display: block;
      position: relative;
      font-size: .18rem;
      line-height: .48rem;
      text-align: left;
      color: $white;
    }
    .navbar-item-content {
      height: auto;
      width: 100%;
    }
    .navbar-icon {
      position: absolute;
      top: .17rem;
      left: .3rem;
      width: 0;
      height: 0;
      border-width: .05rem .05rem .05rem .05rem;
      border-style: solid;
      border-color: transparent transparent transparent $white;
    }
    .navbarActive {
      position: absolute;
      top: .2rem;
      left: .3rem;
      transform: rotate(90deg);
    }
    .navbar-item-link {
      display: block;
      padding-left: .7rem;
      font-size: .18rem;
      line-height: .48rem;
      text-align: left;
      color: #a4aab0;
      &.active,
      &:hover {
        background-color: #095481;
        color: $white;
      }
    }
  }
  .ready {
    padding-left: .1rem;
    font-size: .19rem;
    cursor: pointer;
  }
}
</style>